import { Component, OnInit, Input } from '@angular/core';
import { MemberModel } from 'app/model/member-model';
import { MemberService } from 'app/service/member-service/member.service';

@Component({
  selector: 'app-member-list',
  templateUrl: './member-list.component.html',
  styleUrls: ['./member-list.component.css']
})
export class MemberListComponent implements OnInit {
  memberList : MemberModel[];
  memberTotal : number;
  
  // Pagination Properties
  totalPage : number;
  searchText : string;
  showPage : boolean = false;
  noPage : number = 1 ;
  maxResult : number = 5;
  direction : string = "asc";
  orderBy :string = "idMember";
  beforeNoPage : number =1; 


  constructor(private MemberService: MemberService) { }

  ngOnInit(): void 
  {
    this.searchText = null;
    this.getAllMembersWithPagination();
    this.getCountAllMember();
  }

  counter() {
    if(this.memberTotal == 0)
    {
      this.totalPage = 1;
      this.showPage = false;
    }
    else
    {
      this.showPage = true;
      this.totalPage = Math.ceil(this.memberTotal/this.maxResult);
      console.log("Member Total :" +this.memberTotal );
      console.log("Total Page :" +this.totalPage );
    }
    if(this.noPage > this.totalPage)
    {
      this.noPage  = this.totalPage;
    }
  }

  getAllMembersWithPagination() : void
  {
    this.MemberService.getAllMembersWithPagination
    (this.noPage-1, this.maxResult, this.orderBy, this.direction, this.searchText)
    .subscribe((MemberList) => this.memberList = MemberList);
  }
  
  getCountAllMember()
  {
    this.MemberService.getCountAllMember(this.searchText).subscribe((memberTotal) => 
    {this.memberTotal = memberTotal; this.counter();});
  }

  //pagination
  apply()
  {
    this.noPage = 1; 
    this.getAllMembersWithPagination(); 
    this.counter();
  }

  nextPage() : void
  {
    this.noPage++;
    if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    this.beforeNoPage = this.noPage;
    this.getAllMembersWithPagination();
  }

  previousPage() : void
  {
    this.noPage--;
    if(this.noPage < 1)
    {
      this.noPage=1;
    }
    this.beforeNoPage = this.noPage;
    this.getAllMembersWithPagination();
  }

  inputPage(event) : void
  {
    if(this.noPage < 1 && this.noPage != null)
    {
      this.noPage=1;   
    }
    else if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    event.target.value = this.noPage;
    if(this.noPage != null)
    {
      this.beforeNoPage = this.noPage;
      this.getAllMembersWithPagination();
    }
  }

  firstPage() : void
  {
    this.noPage = 1;
    this.beforeNoPage = this.noPage;
    this.getAllMembersWithPagination();
  }

  lastPage() : void
  {
    this.noPage = this.totalPage;
    this.beforeNoPage = this.noPage;
    this.getAllMembersWithPagination();
  }

  blurInputPage(event) : void
  { 
    if(this.noPage == null)
    {
    event.target.value = this.beforeNoPage;
    this.noPage = this.beforeNoPage;
    }
  }

  search() : void
  {
    this.noPage = 1; 
    this.getAllMembersWithPagination(); 
    this.getCountAllMember();
  }
}
