import { Component, OnInit } from '@angular/core';
import { HomeService } from 'app/service/home-service/home.service';
import { EventModel } from 'app/model/event-model';
import { FotoEventService } from 'app/service/foto-event.service/foto-event.service';
import { FotoRehearsalService } from 'app/service/foto-rehearsal-service/foto-rehearsal.service';
import { FotoRehearsalModel } from 'app/model/foto-rehearsal-model';
import { FotoEventModel } from 'app/model/foto-event-model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(
               private FotoEventService : FotoEventService,
               private FotoRehearsalService : FotoRehearsalService 
              ) { }

  eventList : EventModel[] = [];
  fotoList : FotoEventModel[] = [];
  

  dataLoad : boolean = false;
  ngOnInit(): void {

    this.getFoto();
  }
  
  getRandomInt(max) {
    return Math.floor(Math.random() * Math.floor(max));
  }


  getFoto()
  {
    this.FotoEventService.getCountFotoEvent().subscribe((fotoEventTotal) =>
      {
        var maxResult = 3
        var randomPage = this.getRandomInt(Math.ceil(fotoEventTotal/maxResult));
        this.FotoEventService.getFotoEvent(randomPage, maxResult, "asc").subscribe((fotoList) =>
        {
          this.fotoList = fotoList;
          console.log(this.fotoList.length);
          if(this.fotoList.length < 3)
          {
            this.FotoRehearsalService.getCountFotoRehearsal().subscribe((fotoRehearsalTotal) =>
            {
              maxResult = maxResult - this.fotoList.length;
              randomPage = this.getRandomInt(Math.ceil(fotoRehearsalTotal/maxResult));
              this.FotoRehearsalService.getFotoRehearsal(randomPage, maxResult, "asc").subscribe((fotoList) =>
              {
                fotoList.forEach((foto) => 
                {
                  const idFoto = foto.idFoto;
                  foto.tbRehearsal.tbEvent.deskripsiEvent = foto.tbRehearsal.deskripsiLatihan;
                  const tbEvent = foto.tbRehearsal.tbEvent;
                  const urlFotoEvent = foto.urlFotoRehearsal;
                  this.fotoList.push({idFoto, tbEvent, urlFotoEvent} as FotoEventModel);
                })
              })
            })
          }
          this.dataLoad = true;
        })
      }
    )
  }
}
