import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { EventModel } from 'app/model/event-model';
import { catchError, map, tap } from 'rxjs/operators';
import { RehearsalModel } from 'app/model/rehearsal-model';

@Injectable({
  providedIn: 'root'
})
export class HomeService {
  private homeurl : string = "http://localhost:8081/api/home";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};
  constructor(private http: HttpClient) { }

  getCountNotificationReport(): Observable<number> 
  {
    this.homeurl = "";
    this.homeurl = `http://localhost:8081/api/home/count/notification/event/report`;
    return this.http.get<number>(this.homeurl).pipe(catchError(this.handleError<number>('getCountAllHome')));
  }

  getNotificationReport(noPage : number, maxResult : number, orderBy : string, direction : string): Observable<EventModel[]>
  {
    this.homeurl = "";
    this.homeurl = `http://localhost:8081/api/home/${noPage}/${maxResult}/${orderBy}/${direction}/notification/event/report`;
    return this.http.get<EventModel[]>(this.homeurl).pipe(catchError(this.handleError<EventModel[]>('getHome')));
  }

  getCountNotificationRehearsal(): Observable<number> 
  {
    this.homeurl = "";
    this.homeurl = `http://localhost:8081/api/home/count/notification/rehearsal`;
    return this.http.get<number>(this.homeurl).pipe(catchError(this.handleError<number>('getCountAllHome')));
  }

  getNotificationRehearsal(noPage : number, maxResult : number, orderBy : string, direction : string): Observable<RehearsalModel[]>
  {
    this.homeurl = "";
    this.homeurl = `http://localhost:8081/api/home/${noPage}/${maxResult}/${orderBy}/${direction}/notification/rehearsal`;
    return this.http.get<RehearsalModel[]>(this.homeurl).pipe(catchError(this.handleError<RehearsalModel[]>('getHome')));
  }
  
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
