import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { RehearsalModel } from 'app/model/rehearsal-model';
import { RehearsalService } from 'app/service/rehearsal-service/rehearsal.service';
import { AbsensiService } from 'app/service/absensi-service/absensi.service';
import { AbsensiModel } from 'app/model/absensi-model';
import { LaguModel } from 'app/model/lagu-model';
import { MateriRehearsalService } from 'app/service/materi-rehearsal-service/materi-rehearsal.service';
import { MemberModel } from 'app/model/member-model';
import { FotoRehearsalModel } from 'app/model/foto-rehearsal-model';
import { FotoRehearsalService } from 'app/service/foto-rehearsal-service/foto-rehearsal.service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-rehearsal-details',
  templateUrl: './rehearsal-details.component.html',
  styleUrls: ['./rehearsal-details.component.css']
})

export class RehearsalDetailsComponent implements OnInit {
  rehearsal : RehearsalModel;

  isDeskripsi = true;
  isMemberList = false;
  isMateriList = false;
  isBuktiPembayaran = false;

  constructor(private route: ActivatedRoute,  
              private Router: Router, 
              private RehearsalService: RehearsalService, 
              private AbsensiService : AbsensiService,
              private MateriRehearsalService : MateriRehearsalService,
              private FotoRehearsalService : FotoRehearsalService,
              private modalService: NgbModal
              ) { }

  ngOnInit(): void 
  {
    this.getFotoRehearsalByIdRehearsal();
    this.getCountFotoRehearsalByIdRehearsal();
    this.getRehearsal();
    this.getLaguByIdRehearsal(); 
    this.getCountLaguByIdRehearsal();
    this.getMemberByIdRehearsal();
    this.getCountMemberByIdRehearsal();
  }

  getRehearsal() : void
  {
    const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
    this.RehearsalService.getRehearsal(idRehearsal).subscribe((rehearsal) => this.rehearsal = rehearsal);
  }

  disabledEdit(selesai)
  {
    var today = new Date();
    if(new Date(selesai) < today)
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  goBack(): void {
    this.Router.navigate([`/rehearsal`]);
  }

  onChangeDeskripsi()
  {
    this.tabs(true, false,false, false);
  }

  onChangeMemberList()
  {
    this.tabs(false, true ,false, false);
  }

  onChangeMateriList()
  {
    this.tabs(false,false, true, false);
  }

  onChangeBuktiPembayaran()
  {
    this.tabs( false,false, false, true);
  }

  tabs(isDeskripsi, isMemberList, isMateriList, isBuktiPembayaran) : void
  {
    this.isDeskripsi = isDeskripsi;
    this.isMemberList = isMemberList;
    this.isMateriList = isMateriList;
    this.isBuktiPembayaran = isBuktiPembayaran;
  }
  
  //////////////////////////////////////////////////////MEMBER LIST PANEL////////////////////////////////////////////////////////////
  //Member List Properties
  memberList    : MemberModel[];
  memberTotal : number;

  memberTotalPage : number;
  memberShowPage : boolean = false;;
  memberSearchText : string;
  memberNoPage : number = 1 ;
  memberMaxResult : number = 5;
  memberDirection : string = "asc";
  memberOrderBy :string = "TbMember.idMember";
  memberBeforeNoPage : number = 1 ;
  //Member List Method
  getMemberByIdRehearsal() : void
  {
    const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
    this.AbsensiService.getMemberByIdRehearsal
    (this.memberNoPage-1, this.memberMaxResult, this.memberOrderBy, this.memberDirection, idRehearsal, this.memberSearchText)
    .subscribe((member) => this.memberList = member);
  }

  getCountMemberByIdRehearsal()
  {
    const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
    this.AbsensiService.getCountMemberByIdRehearsal(idRehearsal, this.memberSearchText)
    .subscribe((memberTotal) => {this.memberTotal = memberTotal; this.memberCounter();});
  }
  
  memberCounter() {
    if(this.memberTotal == 0)
    {
      this.memberTotalPage = 1;
      this.memberShowPage = false;
    }
    else
    {
      this.memberShowPage = true;
      this.memberTotalPage = Math.ceil(this.memberTotal/this.memberMaxResult);
      console.log("Member Total :" +this.memberTotal );
      console.log("Total Page :" +this.memberTotalPage );
    }
    if(this.memberNoPage > this.memberTotalPage)
    {
      this.memberNoPage  = this.memberTotalPage;
    }
  }

 //Member List pagination
  memberApply()
  {
    this.memberNoPage = 1; 
    this.getMemberByIdRehearsal(); 
    this.memberCounter();
  }


  memberNextPage() : void
  {
    this.memberNoPage++;
    if(this.memberNoPage > this.memberTotalPage)
    {
      this.memberNoPage=this.memberTotalPage;
    }
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdRehearsal();
  }

  memberPreviousPage() : void
  {
    this.memberNoPage--;
    if(this.memberNoPage < 1)
    {
      this.memberNoPage=1;
    }
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdRehearsal();
  }

  memberInputPage(event) : void
  {
    if(this.memberNoPage < 1 && this.memberNoPage != null)
    {
      this.memberNoPage=1;
    }
    else if(this.memberNoPage > this.memberTotalPage)
    {
      this.memberNoPage=this.memberTotalPage;
    }
    event.target.value = this.memberNoPage;
    if(this.memberNoPage != null)
    {
      this.memberBeforeNoPage = this.memberNoPage;
      this.getMemberByIdRehearsal();
    }
  }

  memberBlurInputPage(event) : void
  { 
    if( this.memberNoPage == null)
    {
    event.target.value = this.memberBeforeNoPage;
    this.memberNoPage = this.memberBeforeNoPage;
    }
  }

  memberFirstPage() : void
  {
    this.memberNoPage = 1; 
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdRehearsal();
  }

  memberLastPage() : void
  {
    this.memberNoPage = this.memberTotalPage; 
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdRehearsal();
  }

  memberSearch() : void
  {
    this.memberNoPage = 1; 
    this.getMemberByIdRehearsal(); 
    this.getCountMemberByIdRehearsal();
  }

  //////////////////////////////////////////////////////MEMBER LIST PANEL////////////////////////////////////////////////////////////
  

//////////////////////////////////////////////////////LAGU LIST PANEL////////////////////////////////////////////////////////////
   //Lagu List Properties
  laguList    : LaguModel[];
  laguTotal : number;

  laguTotalPage : number;
  laguShowPage : boolean = false;;
  laguSearchText : string = "";
  laguNoPage : number = 1 ;
  laguMaxResult : number = 5;
  laguDirection : string = "asc";
  laguOrderBy :string = "TbLagu.idLagu";
  laguBeforeNoPage : number =1; 

  //Lagu List Method
  getLaguByIdRehearsal() : void
  {
    const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
    this.MateriRehearsalService.getLaguByIdRehearsal
    (this.laguNoPage-1, this.laguMaxResult, this.laguOrderBy, this.laguDirection, idRehearsal, this.laguSearchText)
    .subscribe((lagu) => this.laguList = lagu);
  }

  laguCounter() {
    if(this.laguTotal == 0)
    {
      this.laguTotalPage = 1;
      this.laguShowPage = false;
    }
    else
    {
      this.laguShowPage = true;
      this.laguTotalPage = Math.ceil(this.laguTotal/this.laguMaxResult);
    }
    if(this.laguNoPage > this.laguTotalPage)
    {
      this.laguNoPage  = this.laguTotalPage;
    }
  }
  
  getCountLaguByIdRehearsal()
  {
    const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
    this.MateriRehearsalService.getCountLaguByIdRehearsal(idRehearsal, this.laguSearchText)
    .subscribe((laguTotal) => {this.laguTotal = laguTotal; console.log(this.laguTotal); this.laguCounter();});
  }

 //Lagu List pagination
  laguApply()
  {
    this.laguNoPage = 1; 
    this.getLaguByIdRehearsal(); 
    this.laguCounter();
  }

  laguNextPage() : void
  {
    this.laguNoPage++;
    if(this.laguNoPage > this.laguTotalPage)
    {
      this.laguNoPage=this.laguTotalPage;
    }
    this.laguBeforeNoPage = this.laguNoPage;
    this.getLaguByIdRehearsal();
  }

  laguPreviousPage() : void
  {
    this.laguNoPage--;
    if(this.laguNoPage < 1)
    {
      this.laguNoPage=1;
    }
    this.laguBeforeNoPage = this.laguNoPage;
    this.getLaguByIdRehearsal();
  }

  laguInputPage(event) : void
  {
    if(this.laguNoPage < 1 && this.laguNoPage != null)
    {
      this.laguNoPage=1;
    }
    else if(this.laguNoPage > this.laguTotalPage)
    {
      this.laguNoPage=this.laguTotalPage;
    }
    event.target.value = this.laguNoPage;
    if(this.laguNoPage != null)
    {
      this.laguBeforeNoPage = this.laguNoPage;
      this.getLaguByIdRehearsal();
    }
  }

  laguFirstPage() : void
  {
    this.laguNoPage = 1;
    this.laguBeforeNoPage = this.laguNoPage;
    this.getLaguByIdRehearsal();
  }

  laguLastPage() : void
  {
    this.laguNoPage = this.laguTotalPage;
    this.laguBeforeNoPage = this.laguNoPage;
    this.getLaguByIdRehearsal();
  }

  laguBlurInputPage(event) : void
  { 
    if(this.laguNoPage == null)
    {
      event.target.value = this.laguBeforeNoPage;
      this.laguNoPage =this.laguBeforeNoPage;
    }
  }

  laguSearch() : void
  {
    this.laguNoPage = 1; 
    this.getLaguByIdRehearsal(); 
    this.getCountLaguByIdRehearsal();
  }


//////////////////////////////////////////////////////LAGU LIST PANEL////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////FOTO REHEARSAL PANEL////////////////////////////////////////////////////////////

fotoRehearsalList : FotoRehearsalModel[];
fotoRehearsalNoPage : number = 1;
fotoRehearsalTotal : number;
fotoRehearsalTotalPage : number;

getFotoRehearsalByIdRehearsal(): void
{
  const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
  this.FotoRehearsalService.getFotoRehearsalByIdRehearsal(this.fotoRehearsalNoPage-1, 5, "asc", idRehearsal).subscribe((fotoRehearsal) => {this.fotoRehearsalList = fotoRehearsal;});
}

getCountFotoRehearsalByIdRehearsal()
{
  const idRehearsal = +this.route.snapshot.paramMap.get('idRehearsal');
  this.FotoRehearsalService.getCountFotoRehearsalByIdRehearsal(idRehearsal).subscribe((fotoRehearsalTotal) => 
  {
    this.fotoRehearsalTotal = fotoRehearsalTotal; 
    this.fotoRehearsalCounter();
  });
}

  fotoRehearsalCounter() 
  {
    if(this.fotoRehearsalTotal == 0)
    {
      this.fotoRehearsalTotalPage = 1;
    }
    else
    {
      this.fotoRehearsalTotalPage = Math.ceil(this.fotoRehearsalTotal/5);
    }
  }

  nextPageFoto(): void
{
  console.log(this.fotoRehearsalNoPage);
  this.fotoRehearsalNoPage++;
  console.log(this.fotoRehearsalNoPage);
  if(this.fotoRehearsalNoPage > this.fotoRehearsalTotalPage)
  {
    this.fotoRehearsalNoPage  = 1;
  }
  this.getFotoRehearsalByIdRehearsal();
}

previousPageFoto(): void
{
  console.log(this.fotoRehearsalNoPage);
  this.fotoRehearsalNoPage--;
  console.log(this.fotoRehearsalNoPage);
  if(this.fotoRehearsalNoPage < 1)
  {
    this.fotoRehearsalNoPage  = this.fotoRehearsalTotalPage;
  }
  this.getFotoRehearsalByIdRehearsal();
}
//////////////////////////////////////////////////////FOTO REHEARSAL PANEL////////////////////////////////////////////////////////////


  ///////////////////////////////MODAL////////////////////////////
  closeResult = '';
  idFund = '';
  urlFotoBukti = '';

  open(content, idFund, urlFotoBukti) {
    this.idFund = idFund;
    this.urlFotoBukti = urlFotoBukti;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
}



