import { Component, OnInit,  ElementRef, ViewChild } from '@angular/core';
import { EventModel } from 'app/model/event-model';
import { EventService } from 'app/service/event-service/event.service';
import { MateriEventService } from 'app/service/materi-event-service/materi-event.service';
import { Location } from '@angular/common';
import { FormBuilder, FormArray, Validators, FormControl } from '@angular/forms';
import { LaguModel, LaguModelChecked } from 'app/model/lagu-model';
import { MateriEventModel } from 'app/model/materi-event-model';
import {  MemberModelChecked, MemberModel } from 'app/model/member-model';
import { MemberService } from 'app/service/member-service/member.service';
import { AbsensiModel } from 'app/model/absensi-model';
import { MateriRehearsalModel } from 'app/model/materi-rehearsal-model';
import { FormService } from 'app/service/form-service/form.service';
import { Router } from '@angular/router';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {MatAutocompleteSelectedEvent, MatAutocomplete} from '@angular/material/autocomplete';
import {MatChipInputEvent} from '@angular/material/chips';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-rehearsal-add',
  templateUrl: './rehearsal-add.component.html',
  styleUrls: ['./rehearsal-add.component.css']
})
export class RehearsalAddComponent implements OnInit {
  rehearsalForm = this.FormBuilder.group({
    deskripsiLatihan: ['', Validators.required],
    tanggalLatihan: ['', Validators.required],
    lokasiLatihan: ['', Validators.required],
    kehadiranPel: [''],
    tbEvent: ['', Validators.required],
    laguChoice: this.FormBuilder.array([]),
    memberChoice: this.FormBuilder.array([]),
    addFotoRehearsal: this.FormBuilder.array([])
  });

  eventList : EventModel[];
  showKehadiran : boolean;
  MateriEventList : MateriEventModel[];
  AbsensiInput : AbsensiModel[] = [];
  MateriRehearsalInput : MateriRehearsalModel[] = [];
  pilihEvent = false;
  pilihKehadiran = false;
  maxTanggalLatihan : string;
  constructor(private FormBuilder: FormBuilder, 
              private EventService: EventService, 
              private MateriEventService: MateriEventService,
              private MemberService: MemberService,
              private FormService : FormService,
              private Router : Router,
              private modalService: NgbModal,
              private location: Location) 
              { 
                this.filteredmembers = this.memberCtrl.valueChanges.
                pipe(startWith(null), map((searchMember: string | null) => searchMember ? this._filterMember(searchMember) : this.memberList));
                this.filteredlagus = this.laguCtrl.valueChanges.
                pipe(startWith(null), map((searchLagu: string | null) => searchLagu ? this._filterLagu(searchLagu) : this.laguList));
              }

  ngOnInit(): void {
    this.getEvent();
    this.getMember();
    this.rehearsalForm.get("tanggalLatihan").disable();
    this.rehearsalForm.get("kehadiranPel").disable();
    this.memberCtrl.disable();
  }
 
  getEvent(): void
  {
    this.EventService.getCountEventForRehearsalForm().subscribe((count) => 
    {
      if(count > 0 )
      {
        this.EventService.getEventForRehearsalForm(0, count, "idEvent", "asc").subscribe((eventArray) =>  
          { 
            this.eventList = eventArray;
          })
      }
    })
  }

  onChangeTanggalLatihan(): boolean
  {
    var today = new Date();
    const tanggalLatihan = this.rehearsalForm.value.tanggalLatihan ;
    console.log(new Date(tanggalLatihan));
    if(tanggalLatihan != null && tanggalLatihan != "")
    {
      if(new Date(tanggalLatihan) > today)
      {
        this.checkForm(false);
        return false;
      }
      else
      {
        this.checkForm(true);
        return true;
      }
    }
    return false;
  }

  checkForm(enable : boolean)
  {
    if(enable)
    {
      this.memberCtrl.enable();
      this.rehearsalForm.get("kehadiranPel").enable();
      this.rehearsalForm.get("kehadiranPel").setValidators([Validators.required]);
    }
    else
    {
      this.memberCtrl.disable();
      this.memberInputArray.clear();
      this.rehearsalForm.get("kehadiranPel").disable();
    }
  }

  get tbEvent()
  {
    return this.rehearsalForm.get("tbEvent");
  }

  get addFotoRehearsal()
  {
    return this.rehearsalForm.get("addFotoRehearsal") as FormArray;
  }

  onChangeKehadiran() :void
  {
    this.pilihKehadiran = true;
  }

  onChangeEvent() :void
  {
    this.pilihEvent = true;
    this.filteredlagus.subscribe((data) => console.log(data));
    for(let i = this.laguChoice.length-1; i >= 0; i--) {
      this.laguChoice.removeAt(i)
    } 
    this.onChangeEventForTanggalLatihan();
    this.getMateriByIdEvent();
  }

  onChangeEventForTanggalLatihan()
  {
      const event = this.eventList.find((event) => event.idEvent == this.rehearsalForm.value.tbEvent);
      var dd = event.selesai.toString().split("-")[2];
      var mm = event.selesai.toString().split("-")[1]; 
      var yyyy =event.selesai.toString().split("-")[0];
      this.maxTanggalLatihan = yyyy+'-'+mm+'-'+dd;
      this.rehearsalForm.get("tanggalLatihan").enable();
      this.rehearsalForm.get("tanggalLatihan").setValue("");
      this.rehearsalForm.get("kehadiranPel").disable();
      this.memberCtrl.disable();
  }

  goBack(): void {
    this.location.back();
  }

  add()
  {
    
    this.FormService.addRehearsal(this.rehearsalForm.value).subscribe((idRehearsal) =>
    {
      const formData = new FormData();
      this.addFotoRehearsal.controls.forEach((fotoRehearsal) => 
      {
          formData.append('files', fotoRehearsal.value.file);
      });
      this.FormService.addMultipleFotoRehearsal(formData, idRehearsal).subscribe();
      this.Router.navigate([`/rehearsal`])
    })
  }
   
  //////////////////////////////////////////////////////GAMBAR LIST PANEL////////////////////////////////////////////////////////////
  gambarList : {filename: string, url: string}[] = [];
  onFileChange(event) {
    const regex = "^jpeg$|^jpg$|^png$";
    if (event.target.files.length > 0) {
      for(var i = 0; i < event.target.files.length; i++)
      {
        const file = event.target.files[i];

        console.log(file.type.split("/")[1] as string);
        if(file.type.split("/")[1].match(regex) as string)
          {
          this.addFotoRehearsal.push(this.FormBuilder.group({file: file, filename: file.name}));
          
          // Viewing Photo
          let  reader = new FileReader();

          reader.readAsDataURL(file); 
          reader.onload = (_event) => { 
            this.gambarList.push({filename: file.name, url: reader.result as string});
          }
        }
      }
    }
  }

  onFileDrop(event) 
  {
    const regex = "^jpeg$|^jpg$|^png$";
    if (event.length > 0) {
        for(var i = 0; i < event.length; i++)
        {
          const file = event[i];
          console.log(file.type);
          if(file.type.split("/")[1].match(regex) as string)
          {
            this.addFotoRehearsal.push(this.FormBuilder.group({file: file, filename: file.name}));;

            let reader = new FileReader();
            reader.readAsDataURL(file); 
            reader.onload = (_event) => 
            {
              this.gambarList.push({filename: file.name, url: reader.result as string
            });
          }
        }
      }
    }  
  }

  deleteGambar(gambar)
  {
    const index = this.gambarList.findIndex((test) => test.filename == gambar.filename)
    this.gambarList.splice(index, 1);
    const index2 = this.addFotoRehearsal.controls.findIndex((fotoRehearsal) => fotoRehearsal.value.filename == gambar.filename);
    this.addFotoRehearsal.removeAt(index2);

  }
  //////////////////////////////////////////////////////GAMBAR LIST PANEL////////////////////////////////////////////////////////////

  //////////////////////////////////////////////////////MEMBER LIST PANEL////////////////////////////////////////////////////////////
  memberList    : MemberModel[];
  visible = true;
  selectable = true;
  removable = true;
  separatorKeysCodes: number[] = [ENTER, COMMA];
  memberCtrl = new FormControl();
  filteredmembers: Observable<MemberModel[]>;

  @ViewChild('memberInput') memberInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;


   getMember() : void
   {
    this.MemberService.getCountAllMember().subscribe((memberTotal) => 
    {
      if(memberTotal > 0)
      {
        this.MemberService.getAllMembersWithPagination(0, memberTotal, "namaMember", "asc").subscribe((memberList) => 
        {
          this.memberList = memberList;
        });
      }
    });
   }
   
   get memberInputArray()
   {
    return this.rehearsalForm.get("memberChoice") as FormArray;
   }

  removeMember(memberRemove : MemberModel): void {
    const index = this.memberInputArray.controls.findIndex((member) => member.value.idMember == memberRemove.idMember);
    if (index >= 0) {
      this.memberList.push(this.memberInputArray.at(index).value);
      this.memberInputArray.removeAt(index);
    }
  }

  selectedMember(event: MatAutocompleteSelectedEvent): void {
    console.log(event.option.value);
    
    const index = this.memberList.findIndex((member) => member.idMember == event.option.value.idMember);
    this.memberInputArray.push(new FormControl(this.memberList[index]))
    this.memberList.splice(index, 1);
    this.memberInput.nativeElement.value = '';
    this.memberCtrl.setValue(null);

  }

  private _filterMember(value : string): MemberModel[] {
    const filterValue = value.toString().toLowerCase().trim();
    return this.memberList.filter(member => member.namaMember.toLowerCase().indexOf(filterValue) === 0);
  }
   //////////////////////////////////////////////////////MEMBER LIST PANEL////////////////////////////////////////////////////////////

 //////////////////////////////////////////////////////LAGU LIST PANEL////////////////////////////////////////////////////////////
   laguList    : LaguModel[] = [];
   laguCtrl = new FormControl();
   filteredlagus: Observable<LaguModel[]>;
   @ViewChild('laguInput') laguInput: ElementRef<HTMLInputElement>;
 
   getMateriByIdEvent() : void
   {
    this.MateriEventService.getCountLaguByIdEvent(this.tbEvent.value).subscribe((laguTotal) => 
    { 
        if(laguTotal > 0)
        {
        this.MateriEventService.getLaguByIdEvent(0, laguTotal, "TbLagu.pengarang", "asc", this.tbEvent.value).subscribe((laguArray) =>
        {
          this.laguList = laguArray;
          })
        }
      }
    );
  }
 
   get laguChoice()
   {
     return this.rehearsalForm.get('laguChoice') as FormArray;
   }
    
    get laguInputArray()
    {
     return this.rehearsalForm.get("laguChoice") as FormArray;
    }
 
   removeLagu(laguRemove : LaguModel): void {
     const index = this.laguInputArray.controls.findIndex((lagu) => lagu.value.idLagu == laguRemove.idLagu);
     if (index >= 0) {
       this.laguList.push(this.laguInputArray.at(index).value);
       this.laguInputArray.removeAt(index);
     }
   }
 
   selectedLagu(event: MatAutocompleteSelectedEvent): void {
     const index = this.laguList.findIndex((lagu) => lagu.idLagu == event.option.value.idLagu);
     if(index >= 0 )
     {
      this.laguInputArray.push(new FormControl(this.laguList[index]))
      this.laguList.splice(index, 1);
      this.laguInput.nativeElement.value = '';
      this.laguCtrl.setValue(null);
    }
   }
 
   private _filterLagu(value : string): LaguModel[] {
     const filterValue = value.toString().toLowerCase().trim();
     var laguList = this.laguList.filter(lagu => lagu.judulLagu.toLowerCase().indexOf(filterValue) === 0 || lagu.pengarang.toLowerCase().indexOf(filterValue) === 0 );
      return laguList;

   }
 //////////////////////////////////////////////////////LAGU LIST PANEL////////////////////////////////////////////////////////////

   ///////////////////////////////MODAL////////////////////////////
   closeResult = '';

   open(content) {
      this.rehearsalForm.patchValue({tbEvent : this.eventList.find((x) => x.idEvent = this.tbEvent.value )})
     this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', size: 'lg'}).result.then((result) => 
     {
       this.rehearsalForm.patchValue({tbEvent:  this.rehearsalForm.value.tbEvent.idEvent});
       this.closeResult = `Closed with: ${result}`;
     }, (reason) => {
       this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
     });
   }
  
   private getDismissReason(reason: any): string {
     this.rehearsalForm.patchValue({tbEvent:  this.rehearsalForm.value.tbEvent.idEvent});
  
     if (reason === ModalDismissReasons.ESC) {
       return 'by pressing ESC';
     } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
       return 'by clicking on a backdrop';
     } else {
       return `with: ${reason}`;
     }
   }




}
