import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { MemberModel } from 'app/model/member-model';
import { MemberService } from 'app/service/member-service/member.service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, Validators } from '@angular/forms';
import { FormService } from 'app/service/form-service/form.service';

@Component({
  selector: 'app-member-edit',
  templateUrl: './member-edit.component.html',
  styleUrls: ['./member-edit.component.css']
})
export class MemberEditComponent implements OnInit {
  memberForm = this.FormBuilder.group({
    idMember: [''],
    namaMember: ['', [Validators.required, Validators.maxLength(30), Validators.pattern("^[A-Z][a-zA-Z .']*$")]],
    jenisSuara: ['', [Validators.required, Validators.pattern("^Sopran$|^Bass$|^Alto$|^Tenor$")]],
    divisi: ['', [Validators.required, Validators.maxLength(30), Validators.pattern("^[A-Z][a-zA-Z .']*$")]],
    namaAtasan: ['', [Validators.required, Validators.maxLength(30), Validators.pattern("^[A-Z][a-zA-Z .']*$")]],
  });

  statusMember : string;
  constructor(private FormBuilder: FormBuilder, 
                private FormService: FormService, 
                private route: ActivatedRoute,  
                private location: Location, 
                private Router: Router,
                private MemberService : MemberService,
                private modalService: NgbModal
              )
                { }

  ngOnInit(): void 
  {
    console.log("Member Edit Console");
    this.getMember();
  }

  goBack(): void 
  {
    this.location.back();
  }

  getMember() : void
  {
    const idMember = +this.route.snapshot.paramMap.get('idMember');
    this.MemberService.getMember(idMember).subscribe((member) => {this.memberForm.patchValue({
        namaMember: member.namaMember,
        jenisSuara: member.jenisSuara,
        divisi: member.divisi,
        namaAtasan: member.namaAtasan
    })
    this.statusMember = member.statusMember;
    });
  }

  update(): void {
    Object.keys(this.memberForm.controls).forEach
    (
      (key) => this.memberForm.get(key).setValue(this.memberForm.get(key).value.trim())
    );
    const idMember = +this.route.snapshot.paramMap.get('idMember');
    this.idMember.setValue(idMember);
    this.FormService.updateMember(this.memberForm.value as MemberModel ).subscribe(() => this.Router.navigate(["/member/details/" + idMember]));
  }
  
  changeStatus() : void
  {
    const idMember = +this.route.snapshot.paramMap.get('idMember');
    this.FormService.changeStatusMember(idMember).subscribe(() =>
        this.Router.navigate(["/member/details/" + idMember])
    )
  }

  delete(): void
  {
    const idMember = +this.route.snapshot.paramMap.get('idMember');
    this.FormService.deleteMember(idMember).subscribe(() => this.Router.navigate(["/member"]));
  }

  get idMember() { return this.memberForm.get('idMember'); }

  get namaMember() { return this.memberForm.get('namaMember'); }

  get jenisSuara() { return this.memberForm.get('jenisSuara'); }

  get divisi() { return this.memberForm.get('divisi'); }

  get namaAtasan() { return this.memberForm.get('namaAtasan'); }

  ///////////////////////////////MODAL////////////////////////////
  closeResult = '';

  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  
    ///////////////////////////////MODAL////////////////////////////

    openDelete(content) 
    {  
      this.modalService.open(content, {ariaLabelledBy: 'modal-delete-title', size: 'lg'}).result.then((result) => 
      {
        this.closeResult = `Closed with: ${result}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    }

}
