import { Component, OnInit, Input } from '@angular/core';
import { EventModel } from 'app/model/event-model';
import { EventService } from 'app/service/event-service/event.service';

@Component({
  selector: 'app-event-list',
  templateUrl: './event-list.component.html',
  styleUrls: ['./event-list.component.css']
})
export class EventListComponent implements OnInit {
  eventList : EventModel[];
  eventTotal : number;
  
  // Pagination Properties
  totalPage : number;
  searchText : string;
  showPage : boolean = false;
  noPage : number = 1 ;
  maxResult : number = 5;
  direction : string = "asc";
  orderBy :string = "idEvent";
  beforeNoPage : number =1; 


  constructor(private EventService: EventService) { }

  ngOnInit(): void 
  {
    this.searchText = null;
    this.getAllEventsWithPagination();
    this.getCountAllEvent();
  }

  counter() {
    if(this.eventTotal == 0)
    {
      this.totalPage = 1;
      this.showPage = false;
    }
    else
    {
      this.showPage = true;
      this.totalPage = Math.ceil(this.eventTotal/this.maxResult);
      console.log("Event Total :" +this.eventTotal );
      console.log("Total Page :" +this.totalPage );
    }
    if(this.noPage > this.totalPage)
    {
      this.noPage  = this.totalPage;
    }
  }

  getAllEventsWithPagination() : void
  {
    this.EventService.getAllEventsWithPagination
    (this.noPage-1, this.maxResult, this.orderBy, this.direction, this.searchText)
    .subscribe((EventList) => this.eventList = EventList);
  }
  
  getCountAllEvent()
  {
    this.EventService.getCountAllEvent(this.searchText).subscribe((eventTotal) => 
    {this.eventTotal = eventTotal; this.counter();});
  }

  //pagination
  apply()
  {
    this.noPage = 1; 
    this.getAllEventsWithPagination(); 
    this.counter();
  }

  nextPage() : void
  {
    this.noPage++;
    if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    this.beforeNoPage = this.noPage;
    this.getAllEventsWithPagination();
  }

  previousPage() : void
  {
    this.noPage--;
    if(this.noPage < 1)
    {
      this.noPage=1;
    }
    this.beforeNoPage = this.noPage;
    this.getAllEventsWithPagination();
  }

  inputPage(event) : void
  {
    if(this.noPage < 1 && this.noPage != null)
    {
      this.noPage=1;
    }
    else if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    event.target.value = this.noPage;
    if(this.noPage != null)
    {
      this.beforeNoPage = this.noPage;
      this.getAllEventsWithPagination();
    }
  }

  firstPage() : void
  {
    this.noPage = 1;
    this.beforeNoPage = this.noPage;
    this.getAllEventsWithPagination();
  }

  lastPage() : void
  {
    this.noPage = this.totalPage;
    this.beforeNoPage = this.noPage;
    this.getAllEventsWithPagination();
  }

  blurInputPage(event) : void
  { 
    if(this.noPage == null)
    {
    event.target.value = this.beforeNoPage;
    this.noPage = this.beforeNoPage;
    }
  }

  search() : void
  {
    this.noPage = 1; 
    this.getAllEventsWithPagination(); 
    this.getCountAllEvent();
  }
}
