import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { FotoRehearsalModel } from 'app/model/foto-rehearsal-model';
import { FileModel } from 'app/model/file-model';
import { catchError, map, tap } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class FotoRehearsalService {
  private fotorehearsalurl : string = "http://localhost:8081/api/fotorehearsal";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};

  constructor(private http: HttpClient) { }

  getFotoRehearsal(noPage : number, maxResult : number, direction : string): Observable<FotoRehearsalModel[]>
  {
    this.fotorehearsalurl = "";
    this.fotorehearsalurl = `http://localhost:8081/api/fotorehearsal/${noPage}/${maxResult}/idFoto/${direction}/`;
    return this.http.get<FotoRehearsalModel[]>(this.fotorehearsalurl).pipe(catchError(this.handleError<FotoRehearsalModel[]>('getFotoRehearsal', [])));
  }

  getCountFotoRehearsal(): Observable<number>
  {
    this.fotorehearsalurl = "";
    this.fotorehearsalurl = `http://localhost:8081/api/fotorehearsal/count/`;
    return this.http.get<number>(this.fotorehearsalurl).pipe(catchError(this.handleError<number>('getCountFotoRehearsal')));
  }

  getFotoRehearsalByIdRehearsal(noPage : number, maxResult : number, direction : string, idRehearsal : number): Observable<FotoRehearsalModel[]>
  {
    this.fotorehearsalurl = "";
    this.fotorehearsalurl = `http://localhost:8081/api/fotorehearsal/${noPage}/${maxResult}/idFoto/${direction}/idRehearsal/${idRehearsal}`;
    return this.http.get<FotoRehearsalModel[]>(this.fotorehearsalurl).pipe(catchError(this.handleError<FotoRehearsalModel[]>('getFotoRehearsalByIdRehearsal', [])));
  }

  getCountFotoRehearsalByIdRehearsal(idRehearsal : number)
  {
    this.fotorehearsalurl = "";
    this.fotorehearsalurl = `http://localhost:8081/api/fotorehearsal/count/idRehearsal/${idRehearsal}`;
    return this.http.get<number>(this.fotorehearsalurl).pipe(catchError(this.handleError<number>('getCountAllFotoRehearsal')));
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
