import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './component/home/home.component';
import { MemberListComponent } from './component/member/member-list/member-list.component';
import { MemberDetailsComponent } from './component/member/member-details/member-details.component';
import { MemberAddComponent } from './component/member/member-add/member-add.component';
import { MemberEditComponent } from './component/member/member-edit/member-edit.component';
import { EventListComponent } from './component/event/event-list/event-list.component';
import { EventDetailsComponent } from './component/event/event-details/event-details.component';
import { EventAddComponent } from './component/event/event-add/event-add.component';
import { EventEditComponent } from './component/event/event-edit/event-edit.component';
import { RehearsalEditComponent } from './component/rehearsal/rehearsal-edit/rehearsal-edit.component';
import { RehearsalListComponent } from './component/rehearsal/rehearsal-list/rehearsal-list.component';
import { RehearsalAddComponent } from './component/rehearsal/rehearsal-add/rehearsal-add.component';
import { RehearsalDetailsComponent } from './component/rehearsal/rehearsal-details/rehearsal-details.component';
import { FundListComponent } from './component/fund/fund-list/fund-list.component';
import { FundAddComponent } from './component/fund/fund-add/fund-add.component';
import { FundEditComponent } from './component/fund/fund-edit/fund-edit.component';
import { AccessDeniedComponent } from './component/access-denied/access-denied.component';
const routes: Routes = [
  { path: 'home', component: HomeComponent },
  
  //event
  { path: 'event', component: EventListComponent },
  { path: 'event/add', component: EventAddComponent},
  { path: 'event/details/:idEvent', component: EventDetailsComponent},
  { path: 'event/edit/:idEvent', component: EventEditComponent},
  { path: 'event/details',  redirectTo: '/event', pathMatch: 'full'},
  { path: 'event/edit',  redirectTo: '/event', pathMatch: 'full'},

  //member
  { path: 'member', component: MemberListComponent},
  { path: 'member/add', component: MemberAddComponent},
  { path: 'member/details/:idMember', component: MemberDetailsComponent},
  { path: 'member/edit/:idMember', component: MemberEditComponent},
  { path: 'member/details',  redirectTo: '/member', pathMatch: 'full'},
  { path: 'member/edit',  redirectTo: '/member', pathMatch: 'full'},

  //rehearsal
  { path: 'rehearsal', component: RehearsalListComponent},
  { path: 'rehearsal/add', component: RehearsalAddComponent},
  { path: 'rehearsal/details/:idRehearsal', component: RehearsalDetailsComponent},
  { path: 'rehearsal/edit/:idRehearsal', component: RehearsalEditComponent},
  { path: 'rehearsal/details',  redirectTo: '/rehearsal', pathMatch: 'full'},
  { path: 'rehearsal/edit',  redirectTo: '/rehearsal', pathMatch: 'full'},


  //fund
  { path: 'fund', component: FundListComponent},
  { path: 'fund/add', component: FundAddComponent},
  { path: 'fund/edit/:idFund', component: FundEditComponent},
  { path: 'fund/edit',  redirectTo: '/fund', pathMatch: 'full'}, 

  { path: 'access-denied', component: AccessDeniedComponent},
  { path: '', redirectTo: 'home', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
