import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { MemberModel } from 'app/model/member-model';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class MemberService {
  private memberurl : string = "http://localhost:8081/api/member";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};


  constructor(private http: HttpClient) { }

  getAllMembers(): Observable<MemberModel[]> {
    return this.http.get<MemberModel[]>(this.memberurl).pipe(catchError(this.handleError<MemberModel[]>('getAllMembers', [])));
  }

  getAllMembersWithPagination(noPage : number, maxResult : number, orderBy : string, direction : string, search? : string): Observable<MemberModel[]>
  {
    this.memberurl = "";
    this.memberurl = `http://localhost:8081/api/member/${noPage}/${maxResult}/${orderBy}/${direction}`;
    if(search != null && search != "")
    {
      this.memberurl += `/${search}`
    }
    return this.http.get<MemberModel[]>(this.memberurl).pipe(catchError(this.handleError<MemberModel[]>('getAllMembersWithPagination', [])));
  }

  getMember(idMember : number): Observable<MemberModel>
  {
    this.memberurl = "";
    this.memberurl = `http://localhost:8081/api/member/${idMember}`;
    return this.http.get<MemberModel>(this.memberurl).pipe(catchError(this.handleError<MemberModel>('getMember')));
  }

  getCountAllMember(search? : string): Observable<number> 
  {
    this.memberurl = "";
    this.memberurl = `http://localhost:8081/api/member/count`;
    if(search != null && search != "")
    {
      console.log("search buat count member: " + search)
      this.memberurl += `/${search}`
    }
    return this.http.get<number>(this.memberurl).pipe(catchError(this.handleError<number>('getCountAllMember')));
  }

 

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
