import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { RehearsalModel } from 'app/model/rehearsal-model';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RehearsalService {
  private rehearsalurl : string = "http://localhost:8081/api/rehearsal";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};


  constructor(private http: HttpClient) { }

  getAllRehearsals(): Observable<RehearsalModel[]> {
    return this.http.get<RehearsalModel[]>(this.rehearsalurl).pipe(catchError(this.handleError<RehearsalModel[]>('getAllRehearsals', [])));
  }

  getRehearsalByIdEvent(noPage : number, maxResult : number, orderBy : string, direction : string, idEvent : number, search? : string): Observable<RehearsalModel[]>
  {
    this.rehearsalurl = "";
    this.rehearsalurl = `http://localhost:8081/api/rehearsal/${noPage}/${maxResult}/${orderBy}/${direction}/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      this.rehearsalurl += `/${search}`
    }
    return this.http.get<RehearsalModel[]>(this.rehearsalurl).pipe(catchError(this.handleError<RehearsalModel[]>('GetLaguByIdEvent', [])));
  }

  getCountRehearsalByIdEvent(idEvent : number, search? :string) : Observable<number>
  {
    this.rehearsalurl = "";
    this.rehearsalurl = `http://localhost:8081/api/rehearsal/count/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      console.log("search buat count rehearsal: " + search)
      this.rehearsalurl += `/${search}`
    }
    return this.http.get<number>(this.rehearsalurl).pipe(catchError(this.handleError<number>('getCountAllRehearsal')));
  }

  getAllRehearsalsForTbFundForm(noPage : number, maxResult : number, orderBy : string, direction : string, idEvent : number): Observable<RehearsalModel[]>
  {
    this.rehearsalurl = "";
    this.rehearsalurl = `http://localhost:8081/api/rehearsal/${noPage}/${maxResult}/${orderBy}/${direction}/tbfundform/${idEvent}`;
    return this.http.get<RehearsalModel[]>(this.rehearsalurl).pipe(catchError(this.handleError<RehearsalModel[]>('getAllRehearsalsForTbFundForm', [])));
  }

  getAllRehearsalsWithPagination(noPage : number, maxResult : number, orderBy : string, direction : string, search? :string): Observable<RehearsalModel[]>
  {
    this.rehearsalurl = "";
    this.rehearsalurl = `http://localhost:8081/api/rehearsal/${noPage}/${maxResult}/${orderBy}/${direction}`;
    if(search != null && search != "")
    {
      this.rehearsalurl += `/${search}`
    }
    return this.http.get<RehearsalModel[]>(this.rehearsalurl).pipe(catchError(this.handleError<RehearsalModel[]>('getAllRehearsalsWithPagination', [])));
  }

  getRehearsal(idRehearsal : number): Observable<RehearsalModel>
  {
    this.rehearsalurl = "";
    this.rehearsalurl = `http://localhost:8081/api/rehearsal/${idRehearsal}`;
    return this.http.get<RehearsalModel>(this.rehearsalurl).pipe(catchError(this.handleError<RehearsalModel>('getRehearsal')));
  }

  getCountAllRehearsal(search? :string)
  {
    this.rehearsalurl = "";
    this.rehearsalurl = `http://localhost:8081/api/rehearsal/count`;
    if(search != null && search != "")
    {
      console.log("search buat count rehearsal: " + search)
      this.rehearsalurl += `/${search}`
    }
    return this.http.get<number>(this.rehearsalurl).pipe(catchError(this.handleError<number>('getCountAllRehearsal')));
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
