import { Component, OnInit,  ElementRef, ViewChild } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { EventModel } from 'app/model/event-model';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { MateriEventService } from 'app/service/materi-event-service/materi-event.service';
import { LaguService } from 'app/service/lagu-service/lagu.service';
import { LaguModel } from 'app/model/lagu-model';
import { LaguModelChecked } from 'app/model/lagu-model';
import { EventService } from 'app/service/event-service/event.service';

import { MateriEventModel } from 'app/model/materi-event-model';
import { FotoEventModel } from 'app/model/foto-event-model';
import { FotoEventService } from 'app/service/foto-event.service/foto-event.service';
import { FormService } from 'app/service/form-service/form.service';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {MatAutocompleteSelectedEvent, MatAutocomplete} from '@angular/material/autocomplete';
import {MatChipInputEvent} from '@angular/material/chips';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-event-edit',
  templateUrl: './event-edit.component.html',
  styleUrls: ['./event-edit.component.css']
})
export class EventEditComponent implements OnInit {
  eventForm = this.FormBuilder.group({
    idEvent: '',
    namaEvent: [''],
    mulai: ['', Validators.required],
    selesai: ['', Validators.required],
    deskripsiEvent: ['', Validators.required],
    laguChoice: this.FormBuilder.array([]),
    laguTambahan: this.FormBuilder.array([]),
    addFotoEvent: this.FormBuilder.array([]),
    deleteFotoEvent: this.FormBuilder.array([])
  });

  LaguInput : LaguModel[];
  MateriInput : MateriEventModel[] = [];
  LaguTemp : LaguModel[];
  Lagu : LaguModel;
  showButtonDelete : boolean;

  constructor(private route: ActivatedRoute,  
               private location: Location, 
              private FormBuilder: FormBuilder, 
              private EventService: EventService,
              private MateriEventService : MateriEventService,
              private LaguService : LaguService,
              private FormService : FormService,
              private Router : Router,
              private modalService: NgbModal,
              private FotoEventService : FotoEventService) { }


  ngOnInit(): void 
  {
    this.getEvent();
    this.getMateri();
    this.getFotoEvent();
    this.filteredlagus = this.laguCtrl.valueChanges.
    pipe(startWith(null), map((searchLagu: string | null) => searchLagu ? this._filterLagu(searchLagu) : this.laguList));
    this.eventForm.get("namaEvent").disable();
  }

  getEvent() : void
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.EventService.getEvent(idEvent)
    .subscribe((event) => {
      this.eventForm.patchValue
      ({
        idEvent: event.idEvent,
        namaEvent: event.namaEvent,
        mulai: event.mulai,
        selesai: event.selesai,
        deskripsiEvent: event.deskripsiEvent
      });
        this.checkForm(this.checkEvent(event.selesai));
        this.showButtonDelete = this.checkEvent(event.mulai);
    });
  }

  checkForm(enable : boolean)
  {
    if(enable)
    {

      this.eventForm.get("mulai").enable();
      this.eventForm.get("selesai").enable();
      this.eventForm.get("deskripsiEvent").enable();
      this.laguCtrl.enable();
      this.removable = enable;
    }
    else
    {
      
      this.eventForm.get("mulai").disable();
      this.eventForm.get("selesai").disable();
      this.eventForm.get("deskripsiEvent").disable();
      this.laguCtrl.disable();
      this.removable = enable;
    }
  }

  checkEvent(tanggal) : boolean
  {
    var today = new Date();
    if(new Date(tanggal) < today)
    {
      return false;
    }
    else
    {
      return true;
    }
  }

  getMateri() : void
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.MateriEventService.getCountLaguByIdEvent(idEvent).subscribe((totalMateri) => {
      if(totalMateri > 0)
      {
        this.MateriEventService.getLaguByIdEvent(0, totalMateri, 'TbLagu.pengarang', 'asc', idEvent)
        .subscribe((laguArray) =>
          {
            laguArray.forEach((lagu) =>
            {
              this.laguChoice.push(new FormControl(lagu))
            })
            this.getLaguList();
          }
        );
      }
    });
  }

  getFotoEvent() : void
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.FotoEventService.getCountFotoEventByIdEvent(idEvent).subscribe((fotoEventTotal) =>
    {
      if(fotoEventTotal > 0 )
      {
        this.fotoEventTotal = fotoEventTotal;
        this.FotoEventService.getFotoEventByIdEvent(0, fotoEventTotal, "asc", idEvent).subscribe((fotoEventList) =>
        {
          this.fotoEventList = fotoEventList;
          fotoEventList.forEach(fotoEvent => {
            this.gambarList.push({filename: fotoEvent.urlFotoEvent.split("/")[6], url: fotoEvent.urlFotoEvent});
          });
        })
      }
    })
  }

  update()
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.FormService.editEvent(this.eventForm.value).subscribe(() =>
    {
        const formData = new FormData();
        this.addFotoEvent.controls.forEach((fotoEvent) => 
        {
            formData.append('files', fotoEvent.value.file);
        });
        this.FormService.addMultipleFotoEvent(formData, this.eventForm.value.idEvent).subscribe();
        this.Router.navigate([`/event/details/${idEvent}`]);
    })
  }

  goBack()
  {
    this.location.back();
  }

  deleteEvent()
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.FormService.deleteEvent(idEvent).subscribe(() =>
    {
    this.Router.navigate(["/event"]);
    });

  }
  
  onChangeTanggalMulai() : void
  {
    this.eventForm.get("selesai").setValue("");
    this.eventForm.get("selesai").enable();
  }
//////////////////////////////////////////////////////LAGU LIST PANEL////////////////////////////////////////////////////////////
  //Pagination
  laguList : LaguModel[] = [];

  visible = true;
  selectable = true;
  removable = true;
  separatorKeysCodes: number[] = [ENTER, COMMA];

  laguCtrl = new FormControl();
  filteredlagus: Observable<LaguModel[]>;
  @ViewChild('laguInput') laguInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;
  getLaguList()
  {
    this.LaguService.getCountAllLagu().subscribe((totalLagu) => {
      if(totalLagu > 0)
      {
        this.LaguService.getAllLagusWithPagination(0, totalLagu, "pengarang", "asc").subscribe((laguArray) => 
        {
          laguArray.forEach((lagu1) => {
            var index =this.laguChoice.controls.findIndex((lagu2) => lagu2.value.idLagu == lagu1.idLagu)
            if(index < 0)
            {
                this.laguList.push(lagu1);
            }
          })
        })
      }
   })
  }


  get laguChoice() {
    return this.eventForm.get('laguChoice') as FormArray;
  }

  get laguTambahan() {
    return this.eventForm.get('laguTambahan') as FormArray;
  }

  addLaguTambahan(event: MatChipInputEvent)
  {
    const input = event.input;
    const regex = "^[a-zA-Z0-9._ ]+-[a-zA-Z0-9._ ]+";
    console.log(event.value);
    if(event.value.match(regex))
    {
      const pengarang = event.value.split("-")[0].trim();
      const judulLagu = event.value.split("-")[1].trim();
      this.laguTambahan.push(this.FormBuilder.group
      ({
        pengarang: [pengarang], 
        judulLagu: [judulLagu]})
      )
  
      // // Reset the input value
      if (input) {
        input.value = '';
      }
  
      this.laguCtrl.setValue(null);
    }  
  }
  
  removeLagu(laguRemove : LaguModel): void {
    const index = this.laguChoice.controls.findIndex((lagu) => lagu.value.idLagu == laguRemove.idLagu);
    if (index >= 0) {
      this.laguList.push(this.laguChoice.at(index).value);
      this.laguChoice.removeAt(index);
    }
  }

  removeLaguTambahan(laguRemove : LaguModel): void {
    const index = this.laguTambahan.controls.findIndex((lagu) => lagu.value.idLagu == laguRemove.idLagu);
    if (index >= 0) {
      this.laguTambahan.removeAt(index);
    }
  }

  selectedLagu(event: MatAutocompleteSelectedEvent): void {

    const index = this.laguList.findIndex((lagu) => lagu.idLagu == event.option.value.idLagu);
    if(index >= 0 )
    {
      this.laguChoice.push(new FormControl(this.laguList[index]))
      this.laguList.splice(index, 1);
      this.laguInput.nativeElement.value = '';
      this.laguCtrl.setValue(null);
    }
  }

  private _filterLagu(value : string): LaguModel[] {
    const filterValue = value.toString().toLowerCase().trim();
    var laguList = this.laguList.filter(lagu => lagu.judulLagu.toLowerCase().indexOf(filterValue) === 0 || lagu.pengarang.toLowerCase().indexOf(filterValue) === 0 );
     return laguList;
  }


  //////////////////////////////////////////////////////LAGU LIST PANEL////////////////////////////////////////////////////////////

  //////////////////////////////////////////////////////GAMBAR LIST PANEL////////////////////////////////////////////////////////////
    fotoEventList : FotoEventModel[];
    fotoEventTotal : number;
    gambarList : {filename: string, url: string}[] = [];

    get addFotoEvent()
    {
      return this.eventForm.get("addFotoEvent") as FormArray;
    }
  
    get deleteFotoEvent()
    {
      return this.eventForm.get("deleteFotoEvent") as FormArray;
    }
    
    onFileChange(event) {
      const regex = "^jpeg$|^jpg$|^png$";
      if (event.target.files.length > 0) {
        for(var i = 0; i < event.target.files.length; i++)
        {
          const file = event.target.files[i];
  
          console.log(file.type.split("/")[1] as string);
          if(file.type.split("/")[1].match(regex) as string)
            {
            this.addFotoEvent.push(this.FormBuilder.group({file: file, filename: file.name}));
            
            // Viewing Photo
            let  reader = new FileReader();
  
            reader.readAsDataURL(file); 
            reader.onload = (_event) => { 
              this.gambarList.push({filename: file.name, url: reader.result as string});
            }
          }
        }
      }
    }
  
    onFileDrop(event) 
    {
      const regex = "^jpeg$|^jpg$|^png$";
      if (event.length > 0) {
          for(var i = 0; i < event.length; i++)
          {
            const file = event[i];
            console.log(file.type);
            if(file.type.split("/")[1].match(regex) as string)
            {
              this.addFotoEvent.push(this.FormBuilder.group({file: file, filename: file.name}));;
  
              let reader = new FileReader();
              reader.readAsDataURL(file); 
              reader.onload = (_event) => 
              {
                this.gambarList.push({filename: file.name, url: reader.result as string
              });
            }
          }
        }
      }  
    }
  

    deleteGambar(gambar)
    {
      const index = this.gambarList.findIndex((test) => test.filename == gambar.filename)
      this.gambarList.splice(index, 1);
      if(index < this.fotoEventTotal)
      {
        this.fotoEventTotal--;
        const fotoEvent =this.fotoEventList.find((fotoEvent) => fotoEvent.urlFotoEvent == gambar.url);
        this.deleteFotoEvent.push(new FormControl(fotoEvent));
      }
      else
      {
        const index2 = this.addFotoEvent.controls.findIndex((fotoEvent) => fotoEvent.value.filename == gambar.filename);
        this.addFotoEvent.removeAt(index2);
      }
    }
    //////////////////////////////////////////////////////GAMBAR LIST PANEL////////////////////////////////////////////////////////////


   ///////////////////////////////MODAL////////////////////////////
   closeResult = '';

   open(content) {
     this.checkForm(true);
     this.eventForm.get("namaEvent").enable();
     this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', size: 'lg'}).result.then((result) => 
     {
   
       this.checkForm(this.checkEvent(this.eventForm.value.selesai));
       this.eventForm.get("namaEvent").disable();
       this.closeResult = `Closed with: ${result}`;
     }, (reason) => {
        this.checkForm(this.checkEvent(this.eventForm.value.selesai));
        this.eventForm.get("namaEvent").disable();
       this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
     });
   }
  
   private getDismissReason(reason: any): string {
  
     if (reason === ModalDismissReasons.ESC) {
       return 'by pressing ESC';
     } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
       return 'by clicking on a backdrop';
     } else {
       return `with: ${reason}`;
     }
   }

    ///////////////////////////////MODAL////////////////////////////

    openDelete(content) 
    {  
      this.eventForm.get("namaEvent").enable();
      this.modalService.open(content, {ariaLabelledBy: 'modal-delete-title', size: 'lg'}).result.then((result) => 
      {
        this.eventForm.get("namaEvent").disable();
        this.closeResult = `Closed with: ${result}`;
      }, (reason) => {
        this.eventForm.get("namaEvent").disable();
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    }

}
