import { Component, OnInit, Input } from '@angular/core';
import { FundModel } from 'app/model/fund-model';
import { FundService } from 'app/service/fund-service/fund.service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-fund-list',
  templateUrl: './fund-list.component.html',
  styleUrls: ['./fund-list.component.css']
})
export class FundListComponent implements OnInit {
  fundList : FundModel[];
  fundTotal : number;
  
  // Pagination Properties
  totalPage : number;
  searchText : string;
  showPage : boolean = false;
  noPage : number = 1 ;
  maxResult : number = 5;
  direction : string = "asc";
  orderBy :string = "idFund";
  beforeNoPage : number =1; 

  constructor(private FundService: FundService,
                private modalService: NgbModal
                ) { }

  ngOnInit(): void 
  {
    this.searchText = null;
    this.getAllFundsWithPagination();
    this.getCountAllFund();
  }

  disabledEdit(selesai : string)
  {
    var today = new Date();
    today.setDate(today.getDate() - 14);
    if(new Date(selesai) < today)
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  counter() {
    if(this.fundTotal == 0)
    {
      this.totalPage = 1;
      this.showPage = false;
    }
    else
    {
      this.showPage = true;
      this.totalPage = Math.ceil(this.fundTotal/this.maxResult);
    }
    if(this.noPage > this.totalPage)
    {
      this.noPage  = this.totalPage;
    }
  }

  getAllFundsWithPagination() : void
  {
    this.FundService.getAllFundsWithPagination
    (this.noPage-1, this.maxResult, this.orderBy, this.direction, this.searchText)
    .subscribe((FundList) => this.fundList = FundList);
  }
  
  getCountAllFund()
  {
    this.FundService.getCountAllFund(this.searchText).subscribe((fundTotal) => 
    {this.fundTotal = fundTotal; this.counter();});
  }

  //pagination
  apply()
  {
    this.noPage = 1; 
    this.getAllFundsWithPagination(); 
    this.counter();
  }

  nextPage() : void
  {
    this.noPage++;
    if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    this.beforeNoPage = this.noPage;
    this.getAllFundsWithPagination();
  }

  previousPage() : void
  {
    this.noPage--;
    if(this.noPage < 1)
    {
      this.noPage=1;
    }
    this.beforeNoPage = this.noPage;
    this.getAllFundsWithPagination();
  }

  inputPage(event) : void
  {
    if(this.noPage < 1 && this.noPage != null)
    {
      this.noPage=1;
    }
    else if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    event.target.value = this.noPage;
    if(this.noPage != null)
    {
      this.beforeNoPage = this.noPage;
      this.getAllFundsWithPagination();
    }
  }

  firstPage() : void
  {
    this.noPage = 1;
    this.beforeNoPage = this.noPage;
    this.getAllFundsWithPagination();
  }

  lastPage() : void
  {
    this.noPage = this.totalPage;
    this.beforeNoPage = this.noPage;
    this.getAllFundsWithPagination();
  }

  blurInputPage(event) : void
  { 
    if(this.noPage == null)
    {
    event.target.value = this.beforeNoPage;
    this.noPage = this.beforeNoPage;
    }
  }

  search() : void
  {
    this.noPage = 1; 
    this.getAllFundsWithPagination(); 
    this.getCountAllFund();
  }

  ///////////////////////////////MODAL////////////////////////////
  closeResult = '';
  idFund = '';
  urlFotoBukti = '';

  open(content, idFund, urlFotoBukti) {
    this.idFund = idFund;
    this.urlFotoBukti = urlFotoBukti;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
}