import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { FotoEventModel } from 'app/model/foto-event-model';
import { FileModel } from 'app/model/file-model';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FotoEventService {
  private fotoeventurl : string = "http://localhost:8081/api/fotoevent";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};


  constructor(private http: HttpClient) { }

  getFotoEvent(noPage : number, maxResult : number, direction : string): Observable<FotoEventModel[]>
  {
    this.fotoeventurl = "";
    this.fotoeventurl = `http://localhost:8081/api/fotoevent/${noPage}/${maxResult}/idFoto/${direction}/`;
    return this.http.get<FotoEventModel[]>(this.fotoeventurl).pipe(catchError(this.handleError<FotoEventModel[]>('getFotoEvent', [])));
  }

  getCountFotoEvent(): Observable<number>
  {
    this.fotoeventurl = "";
    this.fotoeventurl = `http://localhost:8081/api/fotoevent/count/`;
    return this.http.get<number>(this.fotoeventurl).pipe(catchError(this.handleError<number>('getCountFotoEvent')));
  }

  getFotoEventByIdEvent(noPage : number, maxResult : number, direction : string, idEvent : number): Observable<FotoEventModel[]>
  {
    this.fotoeventurl = "";
    this.fotoeventurl = `http://localhost:8081/api/fotoevent/${noPage}/${maxResult}/idFoto/${direction}/idEvent/${idEvent}`;
    return this.http.get<FotoEventModel[]>(this.fotoeventurl).pipe(catchError(this.handleError<FotoEventModel[]>('getFotoEventByIdEvent', [])));
  }

  getCountFotoEventByIdEvent(idEvent : number)
  {
    this.fotoeventurl = "";
    this.fotoeventurl = `http://localhost:8081/api/fotoevent/count/idEvent/${idEvent}`;
    return this.http.get<number>(this.fotoeventurl).pipe(catchError(this.handleError<number>('getCountAllFotoEvent')));
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
