import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { LaguModel } from 'app/model/lagu-model';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class LaguService {
  private laguurl : string = "http://localhost:8081/api/lagu";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};


  constructor(private http: HttpClient) { }

  getAllLagus(): Observable<LaguModel[]> {
    return this.http.get<LaguModel[]>(this.laguurl).pipe(catchError(this.handleError<LaguModel[]>('getAllLagus', [])));
  }

  getAllLagusWithPagination(noPage : number, maxResult : number, orderBy : string, direction : string, search? : string): Observable<LaguModel[]>
  {
    this.laguurl = "";
    this.laguurl = `http://localhost:8081/api/lagu/${noPage}/${maxResult}/${orderBy}/${direction}`;
    if(search != null && search != "")
    {
      this.laguurl += `/${search}`
    }
    return this.http.get<LaguModel[]>(this.laguurl).pipe(catchError(this.handleError<LaguModel[]>('getAllLagusWithPagination', [])));
  }

  getLagu(idLagu : number): Observable<LaguModel>
  {
    this.laguurl = "";
    this.laguurl = `http://localhost:8081/api/lagu/${idLagu}`;
    return this.http.get<LaguModel>(this.laguurl).pipe(catchError(this.handleError<LaguModel>('getLagu')));
  }

  getCountAllLagu(search? : string): Observable<number> 
  {
    this.laguurl = "";
    this.laguurl = `http://localhost:8081/api/lagu/count`;
    if(search != null && search != "")
    {
      this.laguurl += `/${search}`
    }
    return this.http.get<number>(this.laguurl).pipe(catchError(this.handleError<number>('getCountAllLagu')));
  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
