import { Component, OnInit, Input } from '@angular/core';
import { RehearsalModel } from 'app/model/rehearsal-model';
import { RehearsalService } from 'app/service/rehearsal-service/rehearsal.service';

@Component({
  selector: 'app-rehearsal-list',
  templateUrl: './rehearsal-list.component.html',
  styleUrls: ['./rehearsal-list.component.css']
})
export class RehearsalListComponent implements OnInit {
  rehearsalList : RehearsalModel[];
  rehearsalTotal : number;
  
  // Pagination Properties
  totalPage : number;
  searchText : string;
  showPage : boolean = false;
  noPage : number = 1 ;
  maxResult : number = 5;
  direction : string = "asc";
  orderBy :string = "idRehearsal";
  beforeNoPage : number =1; 

  constructor(private RehearsalService: RehearsalService) { }

  ngOnInit(): void 
  {
    this.searchText = null;
    this.getAllRehearsalsWithPagination();
    this.getCountAllRehearsal();
  }

  disabledEdit(selesai : string)
  {
    var today = new Date();
    if(new Date(selesai) < today)
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  counter() {
    if(this.rehearsalTotal == 0)
    {
      this.totalPage = 1;
      this.showPage = false;
    }
    else
    {
      this.showPage = true;
      this.totalPage = Math.ceil(this.rehearsalTotal/this.maxResult);
      console.log("Rehearsal Total :" +this.rehearsalTotal );
      console.log("Total Page :" +this.totalPage );
    }
    if(this.noPage > this.totalPage)
    {
      this.noPage  = this.totalPage;
    }
  }

  getAllRehearsalsWithPagination() : void
  {
    this.RehearsalService.getAllRehearsalsWithPagination
    (this.noPage-1, this.maxResult, this.orderBy, this.direction, this.searchText)
    .subscribe((RehearsalList) => this.rehearsalList = RehearsalList);
  }
  
  getCountAllRehearsal()
  {
    this.RehearsalService.getCountAllRehearsal(this.searchText).subscribe((rehearsalTotal) => 
    {this.rehearsalTotal = rehearsalTotal; this.counter();});
  }

  //pagination
  apply()
  {
    this.noPage = 1; 
    this.getAllRehearsalsWithPagination(); 
    this.counter();
  }

  nextPage() : void
  {
    this.noPage++;
    if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    this.beforeNoPage = this.noPage;
    this.getAllRehearsalsWithPagination();
  }

  previousPage() : void
  {
    this.noPage--;
    if(this.noPage < 1)
    {
      this.noPage=1;
    }
    this.beforeNoPage = this.noPage;
    this.getAllRehearsalsWithPagination();
  }

  inputPage(event) : void
  {
    if(this.noPage < 1 && this.noPage != null)
    {
      this.noPage=1;
    }
    else if(this.noPage > this.totalPage)
    {
      this.noPage=this.totalPage;
    }
    console.log(this.noPage);
    event.target.value = this.noPage;
     if(this.noPage != null)
    {
      this.beforeNoPage = this.noPage;
      this.getAllRehearsalsWithPagination();
    }
  }

  firstPage() : void
  {
    this.noPage = 1;
    this.beforeNoPage = this.noPage;
      this.getAllRehearsalsWithPagination();
  }

  lastPage() : void
  {
    this.noPage = this.totalPage;
    this.beforeNoPage = this.noPage;
    this.getAllRehearsalsWithPagination();
  }

  blurInputPage(event) : void
  { 
    if(this.noPage == null)
    {
    event.target.value = this.beforeNoPage;
    this.noPage = this.beforeNoPage;
    }
  }

  search() : void
  {
    this.noPage = 1; 
    this.getAllRehearsalsWithPagination(); 
    this.getCountAllRehearsal();
  }
}
