import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { MateriRehearsalModel } from 'app/model/materi-rehearsal-model';
import { catchError, map, tap } from 'rxjs/operators';
import { LaguModel } from 'app/model/lagu-model';
import { RehearsalModel } from 'app/model/rehearsal-model';

@Injectable({
  providedIn: 'root'
})
export class MateriRehearsalService {
  private materiurl : string = "http://localhost:8081/api/materirehearsal";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};

  constructor(private http: HttpClient) { }

  getMateriRehearsal(idMateriRehearsal : number): Observable<MateriRehearsalModel>
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materirehearsal/${idMateriRehearsal}`;
    return this.http.get<MateriRehearsalModel>(this.materiurl).pipe(catchError(this.handleError<MateriRehearsalModel>('getMateriRehearsal')));
  }

  getLaguByIdRehearsal(noPage : number, maxResult : number, orderBy : string, direction : string, idRehearsal : number, search? : string): Observable<LaguModel[]>
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materirehearsal/${noPage}/${maxResult}/${orderBy}/${direction}/idRehearsal/${idRehearsal}`;
    if(search != null && search != "")
    {
      this.materiurl += `/${search}`
    }
    
    return this.http.get<LaguModel[]>(this.materiurl).pipe(catchError(this.handleError<LaguModel[]>('GetLaguByIdRehearsal', [])));
  }

  getCountLaguByIdRehearsal(idRehearsal : number,search? : string ) :Observable<number>
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materirehearsal/count/idRehearsal/${idRehearsal}`;
    if(search != null && search != "")
    {
      this.materiurl += `/${search}`
    }

    return this.http.get<number>(this.materiurl).pipe(catchError(this.handleError<number>('getCountLaguByIdRehearsal')));
  }

  getCountAllMateriRehearsal()
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materirehearsal/count`;
    return this.http.get<number>(this.materiurl).pipe(catchError(this.handleError<number>('getCountAllMateriRehearsal')));
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
