import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { MemberModel } from 'app/model/member-model';
import { MemberService } from 'app/service/member-service/member.service';
import { AbsensiService } from 'app/service/absensi-service/absensi.service';
import { AbsensiModel } from 'app/model/absensi-model';
import { RehearsalModel } from 'app/model/rehearsal-model';

@Component({
  selector: 'app-member-details',
  templateUrl: './member-details.component.html',
  styleUrls: ['./member-details.component.css']
})

export class MemberDetailsComponent implements OnInit {
  member : MemberModel;
  absensiList : AbsensiModel[];
  absensiTotal : number;
  totalPage : number;
  showPage : boolean = false;
  constructor(private route: ActivatedRoute,  
              private MemberService: MemberService, 
              private Router : Router,
              private AbsensiService : AbsensiService) { }

  ngOnInit(): void 
  {
    this.getMember();
    this.getCountRehearsalByIdMember();
    this.getRehearsalByIdMember();
  }

  goBack(): void {
    this.Router.navigate([`/member`]);
  }
  
  counter(maxResult: number) {
    this.totalPage = Math.ceil(this.absensiTotal/maxResult);
    if(this.totalPage == NaN || this.totalPage < 0)
      this.showPage = false;
    else
    {
      return new Array(this.totalPage);
    }
  }

  getMember() : void
  {
    const idMember = +this.route.snapshot.paramMap.get('idMember');
    this.MemberService.getMember(idMember).subscribe((member) => this.member = member);
  }

//////////////////////////////////////////////////////REHEARSAL LIST PANEL////////////////////////////////////////////////////////////
  //Rehearsal List Properties
  rehearsalList    : RehearsalModel[];
  rehearsalTotal : number;

  rehearsalTotalPage : number;
  rehearsalShowPage : boolean = false;;
  rehearsalSearchText : string;
  rehearsalNoPage : number = 1 ;
  rehearsalMaxResult : number = 5;
  rehearsalDirection : string = "asc";
  rehearsalOrderBy :string = "TbRehearsal.idRehearsal";
  rehearsalBeforeNoPage : number = 1 ;
  //Rehearsal List Method
  getRehearsalByIdMember() : void
  {
      const idMember = +this.route.snapshot.paramMap.get('idMember');
      this.AbsensiService.getRehearsalByIdMember
      (this.rehearsalNoPage-1, this.rehearsalMaxResult, this.rehearsalOrderBy, this.rehearsalDirection, idMember, this.rehearsalSearchText)
      .subscribe((rehearsal) => this.rehearsalList = rehearsal);
  }

  rehearsalCounter() {
    if(this.rehearsalTotal == 0)
    {
      this.rehearsalTotalPage = 1;
      this.rehearsalShowPage = false;
    }
    else
    {
      this.rehearsalShowPage = true;
      this.rehearsalTotalPage = Math.ceil(this.rehearsalTotal/this.rehearsalMaxResult);
      console.log("Rehearsal Total :" +this.rehearsalTotal );
      console.log("Total Page :" +this.rehearsalTotalPage );
    }
    if(this.rehearsalNoPage > this.rehearsalTotalPage)
    {
      this.rehearsalNoPage  = this.rehearsalTotalPage;
    }
  }
  
  getCountRehearsalByIdMember()
  {
    const idMember = +this.route.snapshot.paramMap.get('idMember');
    this.AbsensiService.getCountRehearsalByIdMember(idMember, this.rehearsalSearchText)
    .subscribe((rehearsalTotal) => {this.rehearsalTotal = rehearsalTotal; this.rehearsalCounter();});
  }

 //Rehearsal List pagination
  rehearsalApply()
  {
    this.rehearsalNoPage = 1; 
    this.getRehearsalByIdMember(); 
    this.rehearsalCounter();
  }

  rehearsalNextPage() : void
  {
    this.rehearsalNoPage++;
    if(this.rehearsalNoPage > this.rehearsalTotalPage)
    {
      this.rehearsalNoPage=this.rehearsalTotalPage;
    }
    this.rehearsalBeforeNoPage = this.rehearsalNoPage;
    this.getRehearsalByIdMember();
  }

  rehearsalPreviousPage() : void
  {
    this.rehearsalNoPage--;
    if(this.rehearsalNoPage < 1)
    {
      this.rehearsalNoPage=1;
    }
    this.rehearsalBeforeNoPage = this.rehearsalNoPage;
    this.getRehearsalByIdMember();
  }

  rehearsalInputPage(event) : void
  {
    if(this.rehearsalNoPage < 1 && this.rehearsalNoPage != null)
    {
      this.rehearsalNoPage=1;
    }
    else if(this.rehearsalNoPage > this.rehearsalTotalPage)
    {
      this.rehearsalNoPage=this.rehearsalTotalPage;   
    }
    event.target.value = this.rehearsalNoPage;
    if(this.rehearsalNoPage != null)
    {
      this.rehearsalBeforeNoPage = this.rehearsalNoPage;
      this.getRehearsalByIdMember();
    }
  }
  
  rehearsalFirstPage() : void
  {
    this.rehearsalNoPage = 1;
    this.rehearsalBeforeNoPage = this.rehearsalNoPage;
    this.getRehearsalByIdMember();
  }

  rehearsalLastPage() : void
  {
    this.rehearsalNoPage = this.rehearsalTotalPage;
    this.rehearsalBeforeNoPage = this.rehearsalNoPage;
    this.getRehearsalByIdMember();
  }


  rehearsalBlurInputPage(event) : void
  { 
    if(this.rehearsalNoPage == null)
    {
    event.target.value = this.rehearsalBeforeNoPage;
    this.rehearsalNoPage = this.rehearsalBeforeNoPage;
    }
  }

  rehearsalSearch() : void
  {
    this.rehearsalNoPage = 1; 
    this.getRehearsalByIdMember(); 
    this.getCountRehearsalByIdMember();
  }

  //////////////////////////////////////////////////////REHEARSAL LIST PANEL////////////////////////////////////////////////////////////

}
