import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { FundModel } from 'app/model/fund-model';
import { FileModel } from 'app/model/file-model';
import { catchError, map, tap } from 'rxjs/operators';
import { EventModel } from 'app/model/event-model';
import { RehearsalModel } from 'app/model/rehearsal-model';
import { MemberModel } from 'app/model/member-model';

@Injectable({
  providedIn: 'root'
})
export class FormService {
  private formurl : string = "http://localhost:8081/api/form";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};

  constructor(private http: HttpClient) { }

  //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Event,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//
  addEvent(EventAdd: any): Observable<number>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/event`;
    return this.http.post<number>(this.formurl, EventAdd, this.httpOptions).pipe(catchError(this.handleError<number>('addEvent')));
  }

  addMultipleFotoEvent(formData: FormData, idEvent : number): Observable<FileModel> 
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/event/fotoevent/${idEvent}`;
    return this.http.post<any>(this.formurl, formData).pipe(catchError(this.handleError<any>('addMultipleFotoEvent')));
  }

  editEvent(EventEdit: any): Observable<any>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/event`;
    return this.http.put(this.formurl, EventEdit, this.httpOptions).pipe(catchError(this.handleError<FundModel>('editEvent')));
  }

  deleteEvent(event: EventModel | number): Observable<any>
  {
    const id = typeof event === 'number' ? event : event.idEvent;
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/event/${id}`;
    return this.http.delete<EventModel>(this.formurl, this.httpOptions).pipe(catchError(this.handleError<EventModel>('deleteEvent')));

  }
  //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Event,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//

  //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Fund,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//
  addFund(FundAdd: any): Observable<any>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/fund`;
    return this.http.post<number>(this.formurl, FundAdd, this.httpOptions).pipe(catchError(this.handleError<number>('addFund')));
  }

  editFund(FundEdit: any): Observable<any>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/fund`;
    return this.http.put(this.formurl, FundEdit, this.httpOptions).pipe(catchError(this.handleError<FundModel>('updateFund')));
  }

  deleteFund(fund: FundModel | number): Observable<any>
  {
    const id = typeof fund === 'number' ? fund : fund.idFund;
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/fund/${id}`;
    return this.http.delete<FundModel>(this.formurl, this.httpOptions).pipe(catchError(this.handleError<FundModel>('deleteFund')));

  }

  addFotoBukti(formData: FormData, idFund : number): Observable<FileModel>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/fund/fotobukti/${idFund}`;
    return this.http.post<FileModel>(this.formurl, formData).pipe(catchError(this.handleError<FileModel>('addFotoBukti')));
  }
  //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Fund,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//

  //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Rehearsal,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//
  addRehearsal(RehearsalAdd: any): Observable<any>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/rehearsal`;
    return this.http.post<number>(this.formurl, RehearsalAdd, this.httpOptions).pipe(catchError(this.handleError<number>('RehearsalAdd')));
  }

  addMultipleFotoRehearsal(formData: FormData, idRehearsal : number): Observable<FileModel> 
  {
    // console.log(formData.get('file').valueOf)
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/rehearsal/fotorehearsal/${idRehearsal}`;
    return this.http.post<any>(this.formurl, formData).pipe(catchError(this.handleError<any>('addMultipleFotoRehearsal')));
  }

  editRehearsal(RehearsalEdit: any): Observable<any>
  {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/rehearsal`;
    return this.http.put(this.formurl, RehearsalEdit, this.httpOptions).pipe(catchError(this.handleError<FundModel>('editRehersal')));
  }

  deleteRehearsal(rehearsal: RehearsalModel | number): Observable<any>
  {
    const id = typeof rehearsal === 'number' ? rehearsal : rehearsal.idRehearsal;
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/rehearsal/${id}`;
    return this.http.delete<RehearsalModel>(this.formurl, this.httpOptions).pipe(catchError(this.handleError<RehearsalModel>('deleteRehearsal')));

  }

   //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Rehearsal,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//

  //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Member,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//

   addMember(member: any): Observable<MemberModel> 
   {
     this.formurl = "";
     this.formurl = `http://localhost:8081/api/form/member`;
     return this.http.post<MemberModel>(this.formurl, member, this.httpOptions).pipe(catchError(this.handleError<MemberModel>('addMember')));
   }
   
   updateMember(member: any): Observable<any>
   {
     this.formurl = "";
     this.formurl = `http://localhost:8081/api/form/member`;
     return this.http.put(this.formurl, member, this.httpOptions).pipe(catchError(this.handleError<MemberModel>('updateMember')));
   }

   changeStatusMember(idMember: number): Observable<any>
   {
    this.formurl = "";
    this.formurl = `http://localhost:8081/api/form/member/changestatus/${idMember}`;
    return this.http.post<any>(this.formurl, this.httpOptions).pipe(catchError(this.handleError<MemberModel>('updateStatusMember')));
   }
 
   deleteMember(member: MemberModel | number): Observable<any>
   {
     const id = typeof member === 'number' ? member : member.idMember;
     this.formurl = "";
     this.formurl = `http://localhost:8081/api/form/member/${id}`;
     return this.http.delete<MemberModel>(this.formurl, this.httpOptions).pipe(catchError(this.handleError<MemberModel>('deleteMember')));
 
   }

   //.............,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,Member,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,,//
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
