import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { MateriEventModel } from 'app/model/materi-event-model';
import { catchError, map, tap } from 'rxjs/operators';
import { LaguModel } from 'app/model/lagu-model';
import { EventModel } from 'app/model/event-model';

@Injectable({
  providedIn: 'root'
})
export class MateriEventService {
  private materiurl : string = "http://localhost:8081/api/materievent";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};

  constructor(private http: HttpClient) { }

  getMateriEvent(idMateriEvent : number): Observable<MateriEventModel>
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materievent/${idMateriEvent}`;
    return this.http.get<MateriEventModel>(this.materiurl).pipe(catchError(this.handleError<MateriEventModel>('getMateriEvent')));
  }

  getLaguByIdEvent(noPage : number, maxResult : number, orderBy : string, direction : string, idEvent : number, search? : string): Observable<LaguModel[]>
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materievent/${noPage}/${maxResult}/${orderBy}/${direction}/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      this.materiurl += `/${search}`
    }
    
    return this.http.get<LaguModel[]>(this.materiurl).pipe(catchError(this.handleError<LaguModel[]>('GetLaguByIdEvent', [])));
  }

  getCountLaguByIdEvent(idEvent : number,search? : string ) :Observable<number>
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materievent/count/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      this.materiurl += `/${search}`
    }

    return this.http.get<number>(this.materiurl).pipe(catchError(this.handleError<number>('getCountAllMateriEvent')));
  }

  getCountAllMateriEvent()
  {
    this.materiurl = "";
    this.materiurl = `http://localhost:8081/api/materievent/count`;
    return this.http.get<number>(this.materiurl).pipe(catchError(this.handleError<number>('getCountAllMateriEvent')));
  }



  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
