import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { FundModel } from 'app/model/fund-model';
import { FileModel } from 'app/model/file-model';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FundService {
  private fundurl : string = "http://localhost:8081/api/fund";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};


  constructor(private http: HttpClient) { }

  getAllFunds(): Observable<FundModel[]> {
    return this.http.get<FundModel[]>(this.fundurl).pipe(catchError(this.handleError<FundModel[]>('getAllFunds', [])));
  }

  getAllFundsWithPagination(noPage : number, maxResult : number, orderBy : string, direction : string, search? : string): Observable<FundModel[]>
  {
    this.fundurl = "";
    this.fundurl = `http://localhost:8081/api/fund/${noPage}/${maxResult}/${orderBy}/${direction}`;
    if(search != null && search != "")
    {
      this.fundurl += `/${search}`
    }
    return this.http.get<FundModel[]>(this.fundurl).pipe(catchError(this.handleError<FundModel[]>('getAllFundsWithPagination', [])));
  }

  getFundByIdEvent(noPage : number, maxResult : number, orderBy : string, direction : string, idEvent : number,  search? : string): Observable<FundModel[]>
  {
    this.fundurl = "";
    this.fundurl = `http://localhost:8081/api/fund/${noPage}/${maxResult}/${orderBy}/${direction}/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      this.fundurl += `/${search}`
    }
    return this.http.get<FundModel[]>(this.fundurl).pipe(catchError(this.handleError<FundModel[]>('getFundByIdEvent', [])));
  }

  getCountFundByIdEvent( idEvent : number, search? : string): Observable<number>
  {
    this.fundurl = "";
    this.fundurl = `http://localhost:8081/api/fund/count/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      this.fundurl += `/${search}`
    }
    return this.http.get<number>(this.fundurl).pipe(catchError(this.handleError<number>('getCountAllFund')));
  }

  getFund(idFund : number): Observable<FundModel>
  {
    this.fundurl = "";
    this.fundurl = `http://localhost:8081/api/fund/${idFund}`;
    return this.http.get<FundModel>(this.fundurl).pipe(catchError(this.handleError<FundModel>('getFund')));
  }

  getCountAllFund(search : string): Observable<number>
  {
    this.fundurl = "";
    this.fundurl = `http://localhost:8081/api/fund/count`;
    if(search != null && search != "")
    {
      this.fundurl += `/${search}`
    }
    return this.http.get<number>(this.fundurl).pipe(catchError(this.handleError<number>('getCountAllFund')));
  }



  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
