import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { AbsensiModel } from 'app/model/absensi-model';
import { catchError, map, tap } from 'rxjs/operators';
import { MemberModel } from 'app/model/member-model';
import { RehearsalModel } from 'app/model/rehearsal-model';

@Injectable({
  providedIn: 'root'
})
export class AbsensiService {
  private absensiurl : string = "http://localhost:8081/api/absensi";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};

  constructor(private http: HttpClient) { }

 //Dipake buat member edit dan member details
  getRehearsalByIdMember(noPage : number, maxResult : number, orderBy : string, 
    direction : string, idMember : number, search? :string): Observable<RehearsalModel[]>
  {
    this.absensiurl = "";
    this.absensiurl = `http://localhost:8081/api/absensi/${noPage}/${maxResult}/${orderBy}/${direction}/idMember/${idMember}`;
    if(search != null && search != "")
    {
      console.log("search buat rehearsal di member details pake TbAbsensi: " + search)
      this.absensiurl += `/${search}`
    }
    return this.http.get<RehearsalModel[]>(this.absensiurl).pipe(catchError(this.handleError<RehearsalModel[]>('getRehearsalByIdMember')));
  }

  //Dipake buat member edit dan member details
  getCountRehearsalByIdMember(idMember : number,  search? :string): Observable<number>
  {
    this.absensiurl = "";
    this.absensiurl = `http://localhost:8081/api/absensi/count/idMember/${idMember}`;
    if(search != null && search != "")
    {
      console.log("count search buat rehearsal di member details pake TbAbsensi: " + search)
      this.absensiurl += `/${search}`
    }
    return this.http.get<number>(this.absensiurl).pipe(catchError(this.handleError<number>('getCountRehearsalByIdMember')));
  }

  //Dipake buat event details tapi URL API-nya perlu dibenerin
  getMemberByIdEvent(noPage : number, maxResult : number, orderBy : string, 
    direction : string, idEvent : number, search? :string): Observable<MemberModel[]>
  {
    this.absensiurl = "";
    this.absensiurl = `http://localhost:8081/api/absensi/${noPage}
                        /${maxResult}/${orderBy}/${direction}/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      console.log("search buat member di event details pake TbAbsensi: " + search)
      this.absensiurl += `/${search}`
    }
    return this.http.get<MemberModel[]>(this.absensiurl).pipe(catchError(this.handleError<MemberModel[]>('getMemberByIdEvent')));
  }

  //Dipake buat event details tapi URL API-nya perlu dibenerin
  getCountMemberByIdEvent(idEvent : number,  search? :string): Observable<number>
  {
    this.absensiurl = "";
    this.absensiurl = `http://localhost:8081/api/absensi/count/foreventdetails/${idEvent}`;
    if(search != null && search != "")
    {
      console.log("count search buat member di event details pake TbAbsensi: " + search)
      this.absensiurl += `/${search}`
    }
    return this.http.get<number>(this.absensiurl).pipe(catchError(this.handleError<number>('getCountMemberByIdEvent')));
  }

  //Dipake buat rehearsal edit dan rehearsal details
  getMemberByIdRehearsal(noPage : number, maxResult : number, orderBy : string, 
    direction : string, idRehearsal : number, search? :string): Observable<MemberModel[]>
  {
    this.absensiurl = "";
    this.absensiurl = `http://localhost:8081/api/absensi/${noPage}/${maxResult}/${orderBy}/${direction}/idRehearsal/${idRehearsal}`;
    if(search != null && search != "")
    {
      console.log("search buat member di rehearsal details pake TbAbsensi: " + search)
      this.absensiurl += `/${search}`
    }
    return this.http.get<MemberModel[]>(this.absensiurl).pipe(catchError(this.handleError<MemberModel[]>('getMemberByIdRehearsal')));
  }

  //Dipake buat rehearsal edit dan rehearsal details
  getCountMemberByIdRehearsal(idRehearsal : number,  search? :string): Observable<number>
  {
    this.absensiurl = "";
    this.absensiurl = `http://localhost:8081/api/absensi/count/idRehearsal/${idRehearsal}`;
    if(search != null && search != "")
    {
      console.log("count search buat member di rehearsal details pake TbAbsensi: " + search)
      this.absensiurl += `/${search}`
    }
    return this.http.get<number>(this.absensiurl).pipe(catchError(this.handleError<number>('getCountMemberByIdRehearsal')));
  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
