import { Component, OnInit } from '@angular/core';
import { HomeService } from 'app/service/home-service/home.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  notifList : 
  {
    message : string;
    date : Date;
    link : string;
  }[] = [];
  notifTotal : number = 0;

  constructor(private HomeService : HomeService) { }

  ngOnInit(): void {

    this.getNotificationReport();
  }

  getNotificationReport() : void
  {
    this.HomeService.getCountNotificationReport().subscribe((notifTotal) => {
      this.notifTotal =  this.notifTotal + notifTotal;
      if(notifTotal > 0)
      {
        this.HomeService.getNotificationReport(0, notifTotal, "selesai", "desc").subscribe((notifList) => {
          notifList.forEach((notif) =>{
            var message = "<h6> Report untuk Event <b>" + notif.namaEvent + "</b> belum ditarik </h6>";
            var link = `http://localhost:4200/event/details/${notif.idEvent}`;
            var date = notif.selesai;
            this.notifList.push({message, date, link});
          });
        });
     }
    });
    this.HomeService.getCountNotificationRehearsal().subscribe((notifTotal) => {
      this.notifTotal = this.notifTotal + notifTotal;
      if(notifTotal > 0)
      {
        this.HomeService.getNotificationRehearsal(0, notifTotal, "tanggalLatihan", "desc").subscribe((notifList) => {
          notifList.forEach((notif) =>{
            var message = "<h6> Data untuk <b> idRehearsal: " + notif.idRehearsal + "</b> belum diedit </h6>";
            var link = `http://localhost:4200/rehearsal/details/${notif.idRehearsal}`;
            var date = notif.tanggalLatihan;
            this.notifList.push({message, date, link});;
          });
        });
      }
    });
  }

  clickNotif() : void
  {
    this.notifList.sort((a, b) => {return new Date(b.date).getTime() - new Date(a.date).getTime();});
  }

}

