  ngOnInit(): void 
  {
    this.getEvent();
    this.getMemberByIdEvent() ;
    this.getCountMemberByIdEvent();
    this.getFundByIdEvent();
    this.getCountFundByIdEvent();
    this.getLaguByIdEvent();
    this.getCountLaguByIdEvent();
    this.getRehearsalByIdEvent();
    this.getCountRehearsalByIdEvent();
    this.getFotoEventByIdEvent();
    this.getCountFotoEventByIdEvent();
  }

  goBack(): void {
    this.Router.navigate([`/event`]);
  }

  getEvent() : void
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.EventService.getEvent(idEvent).subscribe((event) => this.event = event);
  }

  onChangeDeskripsi()
  {
    this.tabs(true, false,false, false, false);
  }

  onChangeMemberList()
  {
    this.tabs(false, true ,false, false, false);
  }

  onChangeMateriList()
  {
    this.tabs(false,false, true, false, false);
  }

  onChangeRehearsalList()
  {
    this.tabs( false,false, false, true, false);
  }

  onChangeFundList()
  {
    this.tabs( false,false, false, false, true);
  }


  tabs(isDeskripsi, isMemberList, isMateriList, isRehearsalList, isFundList) : void
  {
    this.isDeskripsi = isDeskripsi;
    this.isMemberList = isMemberList;
    this.isMateriList = isMateriList;
    this.isFundList = isFundList;
    this.isRehearsalList = isRehearsalList;
  }


  //////////////////////////////////////////////////////MEMBER LIST PANEL////////////////////////////////////////////////////////////
  //Member List Properties
  memberList    : MemberModel[];
  memberTotal : number;

  memberTotalPage : number;
  memberShowPage : boolean = false;;
  memberSearchText : string;
  memberNoPage : number = 1 ;
  memberMaxResult : number = 5;
  memberDirection : string = "asc";
  memberOrderBy :string = "TbMember.idMember";
  memberBeforeNoPage : number = 1 ;
  //Member List Method
  getMemberByIdEvent() : void
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.AbsensiService.getMemberByIdEvent
    (this.memberNoPage-1, this.memberMaxResult, this.memberOrderBy, this.memberDirection, idEvent, this.memberSearchText)
    .subscribe((member) => this.memberList = member);
  }

  memberCounter() {
    if(this.memberTotal == 0)
    {
      this.memberTotalPage = 1;
      this.memberShowPage = false;
    }
    else
    {
      this.memberShowPage = true;
      this.memberTotalPage = Math.ceil(this.memberTotal/this.memberMaxResult);
      console.log("Member Total :" +this.memberTotal );
      console.log("Total Page :" +this.memberTotalPage );
    }
    if(this.memberNoPage > this.memberTotalPage)
    {
      this.memberNoPage  = this.memberTotalPage;
    }
  }
  
  getCountMemberByIdEvent()
  {
    const idEvent = +this.route.snapshot.paramMap.get('idEvent');
    this.AbsensiService.getCountMemberByIdEvent(idEvent, this.memberSearchText)
    .subscribe((memberTotal) => {this.memberTotal = memberTotal; this.memberCounter();});
  }

 //Member List pagination
  memberApply()
  {
    this.memberNoPage = 1; 
    this.getMemberByIdEvent(); 
    this.memberCounter();
  }

  memberNextPage() : void
  {
    this.memberNoPage++;
    if(this.memberNoPage > this.memberTotalPage)
    {
      this.memberNoPage=this.memberTotalPage;
    }
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdEvent();
  }

  memberPreviousPage() : void
  {
    this.memberNoPage--;
    if(this.memberNoPage < 1)
    {
      this.memberNoPage=1;
    }
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdEvent();
  }

  memberInputPage(event) : void
  {
    if(this.memberNoPage < 1 && this.memberNoPage != null)
    {
      this.memberNoPage=1;
    }
    else if(this.memberNoPage > this.memberTotalPage)
    {
      this.memberNoPage=this.memberTotalPage;
    }
    event.target.value = this.memberNoPage;
    if(this.memberNoPage != null)
    {
      this.memberBeforeNoPage = this.memberNoPage;
      this.getMemberByIdEvent();
    }
  }
  
    memberFirstPage() : void
  {
    this.memberNoPage = 1;
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdEvent();
  }

  memberLastPage() : void
  {
    this.memberNoPage = this.memberTotalPage;
    this.memberBeforeNoPage = this.memberNoPage;
    this.getMemberByIdEvent();
  }

  memberBlurInputPage(event) : void
  { 
    if(this.memberNoPage == null)
    {
      event.target.value = this.memberBeforeNoPage;
      this.memberNoPage =this.memberBeforeNoPage;
    }
  }

  memberSearch() : void
  {
    this.memberNoPage = 1; 
    this.getMemberByIdEvent(); 
    this.getCountMemberByIdEvent();
  }

