import { Component, OnInit } from '@angular/core';
import {  FormBuilder,  Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { EventModel } from 'app/model/event-model';
import { RehearsalModel } from 'app/model/rehearsal-model';
import { RehearsalService } from 'app/service/rehearsal-service/rehearsal.service';
import { EventService } from 'app/service/event-service/event.service';
import { FundService } from 'app/service/fund-service/fund.service';
import { Location } from '@angular/common';
import { FormService } from 'app/service/form-service/form.service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-fund-edit',
  templateUrl: './fund-edit.component.html',
  styleUrls: ['./fund-edit.component.css']
})
export class FundEditComponent implements OnInit {
  fundForm = this.FormBuilder.group({
    idFund: ['', Validators.required],
    jenisTransaksi: ['', Validators.required],
    deskripsiTransaksi: ['', Validators.required],
    nominal: ['', Validators.required],
    tanggalTransaksi: ['', Validators.required],
    tbEvent: ['', Validators.required],
    fotoBukti: ['', Validators.required],
    idRehearsal: [''],
    file : [''],
  });
  checkEditable : boolean = false
  pilihDeskripsi : boolean = true;
  pilihRehearsal : boolean = false;
  isPemasukan : boolean = false;
  isPengeluaran : boolean = false;
  isLainLain : boolean = false;
  isPembayaranPelatih : boolean = false;
  isAdaDiOptionDeskripsi : boolean = false;
  eventList : EventModel[];
  rehearsalList : RehearsalModel[];
  jenisTemp : string;
  deskripsiTemp : string;
  dateNow : String;

  constructor(private location : Location,
              private route: ActivatedRoute, 
              private Router: Router, 
              private FormBuilder: FormBuilder, 
              private FundService : FundService,
              private RehearsalService : RehearsalService, 
              private EventService : EventService,
              private modalService: NgbModal,
              private FormService: FormService) { }

  ngOnInit(): void {
    this.getFund();
    this.getEvent();
    this.initDate();
  }

 initDate()
  {
    var today= new Date();
    var dd = today.getDate().toString();
    var mm = (today.getMonth()+1).toString(); //January is 0!
    var yyyy = today.getFullYear().toString();
    if(today.getDate()<10)
    {
      dd = '0' + dd;
    } 
    if(today.getMonth()<10)
    {
      mm='0'+mm.toString();
    } 
    this.dateNow = yyyy+'-'+mm+'-'+dd;
  }

  getEvent() : void
  {
    this.EventService.getCountEventForFundForm().subscribe( (count) =>
      {
        this.EventService.getEventForFundForm(0, count, "idEvent", "asc").subscribe((eventList) => this.eventList = eventList);
      }
    )
  }

  getRehearsal(idEvent : number)
  {
    this.fundForm.get("idRehearsal").enable();
    this.fundForm.get("idRehearsal").setValidators([Validators.required]);
    this.RehearsalService.getCountRehearsalByIdEvent(idEvent).subscribe((count) =>
      {
        if(count > 0)
        {
          this.RehearsalService.getAllRehearsalsForTbFundForm(0, count, "idRehearsal" ,"asc" , idEvent).subscribe((rehearsalList) => this.rehearsalList = rehearsalList);
        }
      }
    )
  }

  getFund() : void
  {
    const idFund = +this.route.snapshot.paramMap.get('idFund');
    this.FundService.getFund(idFund).subscribe((fund) => 
    {        
      if(this.checkDate(fund.tbEvent.selesai))
      {
        this.checkEditable = true;
        this.fundForm.patchValue({
          idFund: fund.idFund,
          jenisTransaksi: fund.jenisTransaksi,
          deskripsiTransaksi: fund.deskripsiTransaksi,
          nominal: fund.nominal,
          tanggalTransaksi: fund.tanggalTransaksi,
          tbEvent: fund.tbEvent.idEvent,
          fotoBukti: fund.fotoBukti
        });
        if(this.fundForm.get("jenisTransaksi").value == "Pemasukan"){this.isPemasukan = true;}
        else{this.isPengeluaran= true;}
        if(this.fundForm.get("deskripsiTransaksi").value  == "Pemasukan dari Bakorseni" || 
          this.fundForm.get("deskripsiTransaksi").value == "Transfer Kembali ke Bakorseni")
        {
          this.isAdaDiOptionDeskripsi = true;
        }
        this.jenisTemp = this.fundForm.get("jenisTransaksi").value;
        this.deskripsiTemp = this.fundForm.get("deskripsiTransaksi").value;
      }
      else
      {
        this.Router.navigate(["/access-denied"])
      }
    });
  }

  get fileForm()
  {
     return this.fundForm.get('file');
  }

  onChangeJenisTransaksi() : void
  {
    this.pilihDeskripsi = false;
    if(this.fundForm.get("jenisTransaksi").value == "Pemasukan")
    {
      this.isPemasukan = true;
      this.isPengeluaran = false;
      this.isPembayaranPelatih = false;
      this.isLainLain = false;
      this.fundForm.get("deskripsiTransaksi").setValue('');
    }
    else
    {
      this.isPengeluaran = true;
      this.isPemasukan = false;
      this.isPembayaranPelatih = false;
      this.isLainLain = false;
      this.fundForm.get("deskripsiTransaksi").setValue('');
    }
  }

  onChangeDeskripsi() : void
  {
    this.pilihDeskripsi = true;
    if(this.fundForm.get("deskripsiTransaksi").value == "")
    {
      this.fundForm.get("idRehearsal").disable();
      this.isLainLain = true;
      this.isPembayaranPelatih = false;
    }
    else if(this.fundForm.get("deskripsiTransaksi").value == "Pembayaran Pelatih")
    {
      this.isPembayaranPelatih = true;
      this.isLainLain = false;
      this.getRehearsal(this.fundForm.get("tbEvent").value );
    }
    else
    {
      this.fundForm.get("idRehearsal").disable();
      this.isPembayaranPelatih = false;
      this.isLainLain = false;
    }
  }

  checkDate(selesai)
  {
    var today = new Date();
    today.setDate(today.getDate() - 14);
    if(new Date(selesai) < today)
    {
      return false;
    }
    else
    {
      return true;
    }
  }

  onChangeIdEvent() : void
  {
    if(this.fundForm.get("deskripsiTransaksi").value  == "Pembayaran Pelatih")
    {
      this.pilihRehearsal = false;
      this.isPembayaranPelatih = true;
      this.isLainLain = false;
      this.getRehearsal(this.fundForm.get("tbEvent").value);
    }
  }

  onChangeIdRehearsal()
  {
    this.pilihRehearsal = true;
  }


  onFileChange(event) {
    const regex = "^jpeg$|^jpg$|^png$";
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      if(file.type.split("/")[1].match(regex) as string)
      {
        this.fileForm.setValue(file);

        //Viewing Photo
        var reader = new FileReader();
        reader.readAsDataURL(file); 
        reader.onload = (_event) => { 
          this.fundForm.patchValue({fotoBukti : reader.result as string})
        }
      }
    }
  }

  onFileDrop(event) 
  {
    const regex = "^jpeg$|^jpg$|^png$";
    if (event.length > 0) {
      const file = event[0];
      if(file.type.split("/")[1].match(regex) as string)
      {
        this.fileForm.setValue(file);

        var reader = new FileReader();
        reader.readAsDataURL(file); 
        reader.onload = (_event) => {
          this.fundForm.patchValue({fotoBukti : reader.result as string}) 
        }
      }
    }  
  }

  deleteFile()
  {
    this.fileForm.setValue("");
    this.fundForm.patchValue({fotoBukti : ""}) 
  }

  beforeValue : string;
  inputNominal(event)
  {
    if(this.fundForm.value.nominal > 999999999)
    {
        this.fundForm.get("nominal").setValue(this.beforeValue);
    }
    else if (this.fundForm.value.nominal < 0)
    {
        this.fundForm.get("nominal").setValue(0);
    }
    event.target.value = this.fundForm.value.nominal;
    this.beforeValue = this.fundForm.value.nominal;
  }

  blurNominal(event)
  {
    // event.target.value =  parseInt(event.target.value).toLocaleString("id-ID");
  }

    //Button Submit/Go back
  update()
  { 
     this.FormService.editFund(this.fundForm.value).subscribe(() => 
    {
      if(this.fileForm.value != "")
        {
          const formData = new FormData();
          formData.append('file', this.fundForm.value.file);
          this.FormService.addFotoBukti(formData, this.fundForm.value.idFund).subscribe();
        }
        this.fundForm.patchValue({tbEvent: this.fundForm.value.tbEvent.idEvent});
        this.Router.navigate(["/fund"]);
    });
  }

  deleteFund()
  {
    this.FormService.deleteFund(this.fundForm.get("idFund").value).subscribe(() =>
    {   
      this.Router.navigate(["/fund"]);
    });
  }

  goBack()
  {
    this.location.back();
  }
   ///////////////////////////////MODAL////////////////////////////
   closeResult = '';

   open(content) {
    this.fundForm.patchValue({tbEvent: this.eventList.find((event) => event.idEvent == this.fundForm.value.tbEvent)});
     this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', size: 'lg'}).result.then((result) => 
     {
       this.fundForm.patchValue({tbEvent:  this.fundForm.value.tbEvent.idEvent});
       this.closeResult = `Closed with: ${result}`;
     }, (reason) => {
       this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
     });
   }
  
   private getDismissReason(reason: any): string {
     this.fundForm.patchValue({tbEvent:  this.fundForm.value.tbEvent.idEvent});
  
     if (reason === ModalDismissReasons.ESC) {
       return 'by pressing ESC';
     } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
       return 'by clicking on a backdrop';
     } else {
       return `with: ${reason}`;
     }
   }

      ///////////////////////////////MODAL////////////////////////////
   openDelete(content) 
   {  
     this.modalService.open(content, {ariaLabelledBy: 'modal-delete-title', size: 'lg'}).result.then((result) => 
     {
       this.closeResult = `Closed with: ${result}`;
     }, (reason) => {
       this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
     });
   }
  

}
