import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'kehadiran'
})
export class KehadiranPipe implements PipeTransform {

  transform(value: string): string 
  {
    if(value == 'H') return "Hadir";
    else if(value == null || value == '') return "Belum Mulai";
    else return "Tidak Hadir";
  }

}
