import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { EventModel } from 'app/model/event-model';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class EventService {
  private eventurl : string = "http://localhost:8081/api/event";
  httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};
  constructor(private http: HttpClient) { }

  getAllEvents(): Observable<EventModel[]> {
    return this.http.get<EventModel[]>(this.eventurl).pipe(catchError(this.handleError<EventModel[]>('getAllEvents', [])));
  }

  getAllEventsWithPagination(noPage : number, maxResult : number, orderBy : string, direction : string, search? : string): Observable<EventModel[]>
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/${noPage}/${maxResult}/${orderBy}/${direction}`;
    if(search != null && search != "")
    {
      console.log("search buat event: " + search)
      this.eventurl += `/${search}`
    }
    return this.http.get<EventModel[]>(this.eventurl).pipe(catchError(this.handleError<EventModel[]>('getAllEventsWithPagination', [])));
  }

  getCountAllEvent(search? : string): Observable<number> 
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/count`;
    if(search != null && search != "")
    {
      console.log("search buat count event: " + search)
      this.eventurl += `/${search}`
    }
    return this.http.get<number>(this.eventurl).pipe(catchError(this.handleError<number>('getCountAllEvent')));
  }

  getEventForFundForm(noPage : number, maxResult : number, orderBy : string, direction : string, search? : string): Observable<EventModel[]>
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/${noPage}/${maxResult}/${orderBy}/${direction}/forfundform`;
    if(search != null && search != "")
    {
      console.log("search buat event: " + search)
      this.eventurl += `/${search}`
    }
    return this.http.get<EventModel[]>(this.eventurl).pipe(catchError(this.handleError<EventModel[]>('getEventForFundForm', [])));
  }

  getCountEventForFundForm(search? : string): Observable<number> 
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/count/forfundform`;
    if(search != null && search != "")
    {
      console.log("search buat count event: " + search)
      this.eventurl += `/${search}`
    }
    return this.http.get<number>(this.eventurl).pipe(catchError(this.handleError<number>('getCountEventForFundForm')));
  }

  getEventForRehearsalForm(noPage : number, maxResult : number, orderBy : string, direction : string, search? : string): Observable<EventModel[]>
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/${noPage}/${maxResult}/${orderBy}/${direction}/forrehearsalform`;
    if(search != null && search != "")
    {
      console.log("search buat event: " + search)
      this.eventurl += `/${search}`
    }
    return this.http.get<EventModel[]>(this.eventurl).pipe(catchError(this.handleError<EventModel[]>('getEventForRehearsalForm', [])));
  }

  getCountEventForRehearsalForm(search? : string): Observable<number> 
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/count/forrehearsalform`;
    if(search != null && search != "")
    {
      console.log("search buat count event: " + search)
      this.eventurl += `/${search}`
    }
    return this.http.get<number>(this.eventurl).pipe(catchError(this.handleError<number>('getCountEventForRehearsalForm')));
  }
  
  getEvent(idEvent : number): Observable<EventModel>
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/${idEvent}`;
    return this.http.get<EventModel>(this.eventurl).pipe(catchError(this.handleError<EventModel>('getEvent')));
  }

  makeReport(idEvent : number): Observable<any>
  {
    // httpOptions = {headers: new HttpHeaders({ 'Content-Type': 'application/json' })};
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/make/report/${idEvent}`;
    return this.http.get<any>(this.eventurl, {responseType: 'text' as 'json' }).pipe(catchError(this.handleError<any>('getEvent')));
  }

  downloadReport(idEvent : number, filename : string): Observable<any>
  {
    this.eventurl = "";
    this.eventurl = `http://localhost:8081/api/event/download/report/${idEvent}/${filename}`;
    return this.http.get<any>(this.eventurl).pipe(catchError(this.handleError<any>('getEvent')));
  }



  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
